import type {HArg} from './Grammar';
import type {IPutCh} from './CmnInterface';

export interface IAutoPage { (idx: number, str: string): void; }

export class RubySpliter {
	static	#sesame		= '„ÉΩ';
	static	setting(hArg: HArg) {if (hArg.sesame) RubySpliter.#sesame = hArg.sesame}
	static	getSesame() {return RubySpliter.#sesame}

	static	destroy() {RubySpliter.#sesame = '„ÉΩ'}

	#putCh	: IPutCh	= ()=> {};
	init(putCh: IPutCh) {this.#putCh = putCh}

/*
		‚òÖUnicode„Åß„ÄåÊº¢Â≠ó„Äç„ÅÆÊ≠£Ë¶èË°®Áèæ ‚Äì „ÇÇ„ÅÆ„Åã„ÅÆ http://tama-san.com/kanji-regex/
		2E80..2FDF‚ÄÉCJKÈÉ®È¶ñË£úÂä©ÔºãÂ∫∑ÁÜôÈÉ®È¶ñ
		3005‚ÄÉ„ÄÖÔºàÊº¢Â≠ó„ÅÆË∏ä„ÇäÂ≠óÔºâ
		3007‚ÄÉ„ÄáÔºàÊº¢Êï∞Â≠ó„ÅÆ„Çº„É≠Ôºâ
		303B‚ÄÉ„ÄªÔºàÊº¢Â≠ó„ÅÆË∏ä„ÇäÂ≠óÔºâ
		3400..4DBF‚ÄÉCJKÁµ±ÂêàÊº¢Â≠óÊã°ÂºµA
		4E00..9FFF‚ÄÉCJKÁµ±ÂêàÊº¢Â≠ó
		F900..FAFF‚ÄÉCJK‰∫íÊèõÊº¢Â≠ó
		20000..2FFFF‚ÄÉCJKÁµ±ÂêàÊº¢Â≠óÊã°ÂºµB„ÄúFÔºãCJK‰∫íÊèõÊº¢Â≠óËøΩÂä†ÔºãÂøµ„ÅÆ„Åü„ÇÅU+2FFFF„Åæ„Åß

		[\x{2E80}-\x{2FDF}„ÄÖ„Äá„Äª\x{3400}-\x{4DBF}\x{4E00}-\x{9FFF}\x{F900}-\x{FAFF}\x{20000}-\x{2FFFF}]
		[\u2E80-\u2FDF„ÄÖ„Äá„Äª\u3400-\u4DBF\u4E00-\u9FFF\uF900-\uFAFF\u20000-\u2FFFF]
		[‚∫Ä-‚øü„ÄÖ„Äá„Äª„êÄ-‰∂ø‰∏Ä-ÈøøÔ§Ä-Ô´ø\u20000-\u2FFFF]		// Âê´„Åæ„Çå„Å™„ÅÑÊñáÂ≠ó„Åå„ÅÇ„Çã
		[‚∫Ä-‚øü„ÄÖ„Äá„Äª„êÄ-ÈøøË±à-Ô´ø\u20000-\u2FFFF]			// „ÉΩ--30FD „ÅåÂ§â„Å´Âºï„Å£„Åã„Åã„Çã„ÄÇÂ§öÂàÜ\u2000-\u2FFFËß£Èáà
		\\u{20000}-\\u{2FFFF}	// ‰∫îÊ°Å„Å†„Å®„Ç®„É©„Éº

		„Äê2022/10/03„ÄërubyÊ≠£Ë¶èË°®Áèæ„ÅÆUnicode „Éó„É≠„Éë„ÉÜ„Ç£(„Å®POSIXÊñáÂ≠ó„ÇØ„É©„Çπ) - Qiita https://qiita.com/Takayuki_Nakano/items/8d38beaddb84b488d683
			> „Åì„ÅÆHiragana„Éó„É≠„Éë„ÉÜ„Ç£„ÄÅÈï∑Èü≥Ë®òÂè∑„ÅØÂê´„Åæ„Çå„Å¶„ÅÑ„Åæ„Åõ„Çì„ÄÇ
			> \p{Han}‚Ä¶Á∞°‰ΩìÂ≠ó„ÇÑÁπÅ‰ΩìÂ≠ó„ÄÅÈüìÂõΩË™û„ÅÆÊº¢Â≠ó‚Ä¶„Éô„Éà„Éä„É†Ë™û„ÅÆÊº¢Â≠ó„Å´„ÇÇ„Éû„ÉÉ„ÉÅ
		
		„ÉªUnicodeÊñáÂ≠ó‰∏ÄË¶ßË°® - instant tools https://tools.m-bsys.com/ex/unicode_table.php
*/
	static	#REG_RUBY	: RegExp;
	static	setEscape(ce: string) {
		// 830 match 11293 step(1.7ms) PCRE2 https://regex101.com/r/BBrQtC/1
		/*
(?<txt4>\\\S)
|	ÔΩú(?<str>[^„Ää\n]+)„Ää(?<ruby>[^„Äã\n]+)„Äã
|	(?: (?<kan>[‚∫Ä-‚øü„ÄÖ„Äá„Äª„êÄ-ÈøøË±à-Ô´ø]+ [„ÅÅ-„Éø]* | [^„ÄÄÔΩú„Ää„Äã\n])
		„Ää(?<kan_ruby>[^„Äã\n]+)„Äã)
|	(?<txt>
	[\xD800-\xDBFF][\xDC00-\xDFFF]
|	[^ÔΩú„Ää„Äã]+?
|	.)
		*/
		RubySpliter.#REG_RUBY = new RegExp(
			`${ce ?`(?<ce>\\${ce}\\S)|` :''}`+
			`ÔΩú(?<str>[^„Ää\\n]+)„Ää(?<ruby>[^„Äã\\n]+)„Äã`+
			`|(?:(?<kan>[‚∫Ä-‚øü„ÄÖ„Äá„Äª„êÄ-ÈøøË±à-Ô´ø]+[„ÅÅ-„Éø]*|[^„ÄÄÔΩú„Ää„Äã\\n])`+
			`„Ää(?<kan_ruby>[^„Äã\\n]+)„Äã)`+
			`|(?<txt>`+
			`[\uD800-\uDBFF][\uDC00-\uDFFF]`+	// ‰∏ä‰Ωç + ‰∏ã‰Ωç„Çµ„É≠„Ç≤„Éº„Éà
			`|[^ÔΩú„Ää„Äã]+?`+		// ‰∏çË¶Å„Å†„ÅåÁ¥∞Âàá„Çå„Å´„Åó„Å™„ÅÑ„Åª„ÅÜ„ÅåÂæå„ÄÖÂäπÁéá„ÅßÊúâÂà©
			`|.)`,
			'gs'
		);
	}

	putTxt(text: string) {
		for (const {groups} of text.matchAll(RubySpliter.#REG_RUBY)) {
			const {ruby, kan_ruby, kan='', ce, txt='', str=''} = groups!;
			if (ruby) {this.putTxtRb(decodeURIComponent(str), ruby); continue}

			if (kan_ruby) {this.putTxtRb(kan, kan_ruby); continue}
			if (ce) {this.#putCh(ce.slice(1), ''); continue}

			for (const v of Array.from(txt)) this.#putCh(v, '');
				// txt.split('')„ÇÑ [...txt] „ÅØ„Çµ„É≠„Ç≤„Éº„Éà„Éö„Ç¢„ÅßÂïèÈ°å
		}
	}

	putTxtRb(text: string, ruby: string) {	// „ÉÜ„Çπ„ÉàÁî®„Å´public
		// Ëá™ÂãïÂå∫Âàá„Çä„ÇíË°å„Çè„Å™„ÅÑÔºàÂÜÖÈÉ®ÁöÑ jsonÊñáÊ≥ïÔºâ
		if (/^\w+ÔΩú{"/.test(ruby)) {this.#putCh(text, ruby); return}

		const a: string[] = Array.from(text);
		const len = a.length;
		if (/^\*.?$/.test(ruby)) {	// ÂÇçÁÇπÊñáÊ≥ï
			const rb_ses = 'centerÔΩú'+ (ruby === '*' ?RubySpliter.#sesame :ruby.charAt(1));
			for (let i=0; i<len; ++i) this.#putCh(a[i]!, rb_ses);
			return;
		}

		// Ëá™ÂãïÂå∫Âàá„Çä„ÇíË°å„Çè„Å™„ÅÑÔºàÂçòÊº¢Â≠ó„Å™„Å©Ôºâ
		if (len === 1 || ! ruby.includes(' ')) {
			this.#putCh(text, decodeURIComponent(ruby));
			return;
		}

		// Á©∫ÁôΩ„Åå„ÅÇ„ÇãÂ†¥Âêà„ÅØ„É¶„Éº„Ç∂„ÅåÂå∫Âàá„Çä„ÇíÊåáÂÆö„Åó„Å¶„ÅÑ„Çã„ÇÇ„ÅÆ„Å®Ë¶ã„Å™„Åô
		const aR = ruby.split(' ');
		const lenR = aR.length;
		const len_max = (lenR > len) ?lenR :len;
		for (let i=0; i<len_max; ++i) this.#putCh(
			(i < len) ? a[i]! : '',
			(i < lenR) ? decodeURIComponent(aR[i]!) : ''
		);
	}

}
