const defË°åÈ†≠Á¶ÅÂâá	= '„ÄÅ„ÄÇÔºåÔºéÔºâÔºΩÔΩù„Äâ„Äç„Äè„Äë„Äï‚Äù„Äü„ÅÅ„ÅÉ„ÅÖ„Åá„Åâ„Å£„ÇÉ„ÇÖ„Çá„Çé„Ç°„Ç£„Ç•„Çß„Ç©„ÉÉ„É£„É•„Éß„ÉÆ„Éµ„É∂ÔºÅÔºü!?‚Äº‚Åâ„Éª„Éº„Çù„Çû„ÉΩ„Éæ„ÄÖ';
const defË°åÊú´Á¶ÅÂâá	= 'ÔºªÔºàÔΩõ„Äà„Äå„Äé„Äê„Äî‚Äú„Äù';
const defÂàÜÂâ≤Á¶ÅÊ≠¢	= '‚îÄ‚Ä•‚Ä¶';
const def„Å∂„Çâ‰∏ã„Åí	= defË°åÈ†≠Á¶ÅÂâá;

const defRegË°åÈ†≠Á¶ÅÂâá	= new RegExp(`[${defË°åÈ†≠Á¶ÅÂâá}]`);
const defRegË°åÊú´Á¶ÅÂâá	= new RegExp(`[${defË°åÊú´Á¶ÅÂâá}]`);
const defRegÂàÜÂâ≤Á¶ÅÊ≠¢	= new RegExp(`[${defÂàÜÂâ≤Á¶ÅÊ≠¢}]`);
const defReg„Å∂„Çâ‰∏ã„Åí	= defRegË°åÈ†≠Á¶ÅÂâá;


export class Hyphenation {
	#sË°åÈ†≠Á¶ÅÂâá		= defË°åÈ†≠Á¶ÅÂâá;
	#sË°åÊú´Á¶ÅÂâá		= defË°åÊú´Á¶ÅÂâá;
	#sÂàÜÂâ≤Á¶ÅÊ≠¢		= defÂàÜÂâ≤Á¶ÅÊ≠¢;
	#s„Å∂„Çâ‰∏ã„Åí		= def„Å∂„Çâ‰∏ã„Åí;
	get Ë°åÈ†≠Á¶ÅÂâá() {return this.#sË°åÈ†≠Á¶ÅÂâá}
	get Ë°åÊú´Á¶ÅÂâá() {return this.#sË°åÊú´Á¶ÅÂâá}
	get ÂàÜÂâ≤Á¶ÅÊ≠¢() {return this.#sÂàÜÂâ≤Á¶ÅÊ≠¢}
	get „Å∂„Çâ‰∏ã„Åí() {return this.#s„Å∂„Çâ‰∏ã„Åí}

	#regË°åÈ†≠Á¶ÅÂâá	= defRegË°åÈ†≠Á¶ÅÂâá;
	#regË°åÊú´Á¶ÅÂâá	= defRegË°åÊú´Á¶ÅÂâá;
	#regÂàÜÂâ≤Á¶ÅÊ≠¢	= defRegÂàÜÂâ≤Á¶ÅÊ≠¢;
	#reg„Å∂„Çâ‰∏ã„Åí	= defReg„Å∂„Çâ‰∏ã„Åí;

	break_fixed		= false;
	break_fixed_left= 0;
	break_fixed_top	= 0;
	bura			= false;
	lay(hArg: HArg) {
		if (hArg.kinsoku_sol) {
			this.#sË°åÈ†≠Á¶ÅÂâá = hArg.kinsoku_sol;
			this.#regË°åÈ†≠Á¶ÅÂâá = new RegExp(`[${this.#sË°åÈ†≠Á¶ÅÂâá}]`);
		}
		if (hArg.kinsoku_eol) {
			this.#sË°åÊú´Á¶ÅÂâá = hArg.kinsoku_eol;
			this.#chkÁ¶ÅÂâá„ÅÆÁ´∂Âêà_„Å∂„ÇâË°åÊú´();
			this.#regË°åÊú´Á¶ÅÂâá = new RegExp(`[${this.#sË°åÊú´Á¶ÅÂâá}]`);
		}
		if (hArg.kinsoku_dns) {
			this.#sÂàÜÂâ≤Á¶ÅÊ≠¢ = hArg.kinsoku_dns;
			this.#chkÁ¶ÅÂâá„ÅÆÁ´∂Âêà_„Å∂„ÇâÂàÜÁ¶Å();
			this.#regÂàÜÂâ≤Á¶ÅÊ≠¢ = new RegExp(`[${this.#sÂàÜÂâ≤Á¶ÅÊ≠¢}]`);
		}
		if (hArg.kinsoku_bura) {
			this.#s„Å∂„Çâ‰∏ã„Åí = hArg.kinsoku_bura;
			this.#chkÁ¶ÅÂâá„ÅÆÁ´∂Âêà_„Å∂„ÇâË°åÊú´();
			this.#chkÁ¶ÅÂâá„ÅÆÁ´∂Âêà_„Å∂„ÇâÂàÜÁ¶Å();
			this.#reg„Å∂„Çâ‰∏ã„Åí = new RegExp(`[${this.#s„Å∂„Çâ‰∏ã„Åí}]`);
		}
		if ('bura' in hArg) this.bura = argChk_Boolean(hArg, 'bura', false);

		this.break_fixed		= argChk_Boolean(hArg, 'break_fixed', this.break_fixed);
		this.break_fixed_left	= argChk_Num(hArg, 'break_fixed_left', this.break_fixed_left);
		this.break_fixed_top	= argChk_Num(hArg, 'break_fixed_top', this.break_fixed_top);
	}
		// Á¶ÅÂâá„ÅÆÁ´∂ÂêàÔºà„Å∂„Çâ‰∏ã„Åí „Å® Ë°åÊú´Á¶ÅÂâá „ÅÆ‰∏°Êñπ„Å´Âê´„Åæ„Çå„ÇãÊñáÂ≠ó„Åå„ÅÇ„Å£„Å¶„ÅØ„Å™„Çâ„Å™„ÅÑÔºâ
		#chkÁ¶ÅÂâá„ÅÆÁ´∂Âêà_„Å∂„ÇâË°åÊú´() {
			// Âá¶ÁêÜÈÄüÂ∫¶ÊúÄÈÅ©Âåñ„Åß‰∏ªÂæì„ÇíÂ§â„Åà„Çã„ÄÇÈï∑„ÅÑÊñπ„Çí„Éç„Ç§„ÉÜ„Ç£„Éñ„Å´„ÇÑ„Çâ„Åõ„Çã
			const lenË°åÊú´Á¶ÅÂâá = this.#sË°åÊú´Á¶ÅÂâá.length;
			const len„Å∂„Çâ‰∏ã„Åí = this.#s„Å∂„Çâ‰∏ã„Åí.length;
			if (lenË°åÊú´Á¶ÅÂâá < len„Å∂„Çâ‰∏ã„Åí) {
				for (let i=0; i<lenË°åÊú´Á¶ÅÂâá; ++i) {
					const c = this.#sË°åÊú´Á¶ÅÂâá[i];
					if (this.#s„Å∂„Çâ‰∏ã„Åí.includes(c!)) throw `Á¶ÅÂâá„ÅÆÁ´∂Âêà„Åå„ÅÇ„Çä„Åæ„Åô„ÄÇÊñáÂ≠ó ${c} „Åå„Å∂„Çâ‰∏ã„Åí „Å® Ë°åÊú´Á¶ÅÂâá „ÅÆ‰∏°Êñπ„Å´Âê´„Åæ„Çå„Åæ„Åô`;
				}
			}
			else {
				for (let i=0; i<len„Å∂„Çâ‰∏ã„Åí; ++i) {
					const c = this.#s„Å∂„Çâ‰∏ã„Åí[i];
					if (this.#sË°åÊú´Á¶ÅÂâá.includes(c!)) throw `Á¶ÅÂâá„ÅÆÁ´∂Âêà„Åå„ÅÇ„Çä„Åæ„Åô„ÄÇÊñáÂ≠ó ${c} „Åå„Å∂„Çâ‰∏ã„Åí „Å® Ë°åÊú´Á¶ÅÂâá „ÅÆ‰∏°Êñπ„Å´Âê´„Åæ„Çå„Åæ„Åô`;
				}
			}
		}
		// Á¶ÅÂâá„ÅÆÁ´∂ÂêàÔºà„Å∂„Çâ‰∏ã„Åí „Å® ÂàÜÂâ≤Á¶ÅÊ≠¢ „ÅÆ‰∏°Êñπ„Å´Âê´„Åæ„Çå„ÇãÊñáÂ≠ó„Åå„ÅÇ„Å£„Å¶„ÅØ„Å™„Çâ„Å™„ÅÑÔºâ
		#chkÁ¶ÅÂâá„ÅÆÁ´∂Âêà_„Å∂„ÇâÂàÜÁ¶Å() {
			// Âá¶ÁêÜÈÄüÂ∫¶ÊúÄÈÅ©Âåñ„Åß‰∏ªÂæì„ÇíÂ§â„Åà„Çã„ÄÇÈï∑„ÅÑÊñπ„Çí„Éç„Ç§„ÉÜ„Ç£„Éñ„Å´„ÇÑ„Çâ„Åõ„Çã
			const lenÂàÜÂâ≤Á¶ÅÊ≠¢ = this.#sÂàÜÂâ≤Á¶ÅÊ≠¢.length;
			const len„Å∂„Çâ‰∏ã„Åí = this.#s„Å∂„Çâ‰∏ã„Åí.length;
			if (lenÂàÜÂâ≤Á¶ÅÊ≠¢ < len„Å∂„Çâ‰∏ã„Åí) {
				for (let i=0; i<lenÂàÜÂâ≤Á¶ÅÊ≠¢; ++i) {
					const c = this.#sÂàÜÂâ≤Á¶ÅÊ≠¢[i];
					if (this.#s„Å∂„Çâ‰∏ã„Åí.includes(c!)) throw `Á¶ÅÂâá„ÅÆÁ´∂Âêà„Åå„ÅÇ„Çä„Åæ„Åô„ÄÇÊñáÂ≠ó ${c} „Åå„Å∂„Çâ‰∏ã„Åí „Å® ÂàÜÂâ≤Á¶ÅÊ≠¢ „ÅÆ‰∏°Êñπ„Å´Âê´„Åæ„Çå„Åæ„Åô`;
				}
			}
			else {
				for (let i=0; i<len„Å∂„Çâ‰∏ã„Åí; ++i) {
					const c = this.#s„Å∂„Çâ‰∏ã„Åí[i];
					if (this.#sÂàÜÂâ≤Á¶ÅÊ≠¢.includes(c!)) throw `Á¶ÅÂâá„ÅÆÁ´∂Âêà„Åå„ÅÇ„Çä„Åæ„Åô„ÄÇÊñáÂ≠ó ${c} „Åå„Å∂„Çâ‰∏ã„Åí „Å® ÂàÜÂâ≤Á¶ÅÊ≠¢ „ÅÆ‰∏°Êñπ„Å´Âê´„Åæ„Çå„Åæ„Åô`;
				}
			}
		}

	reNew(to: Hyphenation) {
		to.#genKinsoku(this.#sË°åÈ†≠Á¶ÅÂâá, this.#sË°åÊú´Á¶ÅÂâá, this.#sÂàÜÂâ≤Á¶ÅÊ≠¢, this.#s„Å∂„Çâ‰∏ã„Åí);

		to.break_fixed		= this.break_fixed;
		to.break_fixed_left	= this.break_fixed_left;
		to.break_fixed_top	= this.break_fixed_top;
		to.bura				= this.bura;
	}
		#genKinsoku(sË°åÈ†≠Á¶ÅÂâá: string, sË°åÊú´Á¶ÅÂâá: string, sÂàÜÂâ≤Á¶ÅÊ≠¢: string, s„Å∂„Çâ‰∏ã„Åí: string) {
			if (this.#sË°åÈ†≠Á¶ÅÂâá	!= sË°åÈ†≠Á¶ÅÂâá) {
				this.#sË°åÈ†≠Á¶ÅÂâá	= sË°åÈ†≠Á¶ÅÂâá;
				this.#regË°åÈ†≠Á¶ÅÂâá = new RegExp(`[${sË°åÈ†≠Á¶ÅÂâá}]`);
			}
			if (this.#sË°åÊú´Á¶ÅÂâá	!= sË°åÊú´Á¶ÅÂâá) {
				this.#sË°åÊú´Á¶ÅÂâá	= sË°åÊú´Á¶ÅÂâá;
				this.#regË°åÊú´Á¶ÅÂâá = new RegExp(`[${sË°åÊú´Á¶ÅÂâá}]`);
			}
			if (this.#sÂàÜÂâ≤Á¶ÅÊ≠¢	!= sÂàÜÂâ≤Á¶ÅÊ≠¢) {
				this.#sÂàÜÂâ≤Á¶ÅÊ≠¢	= sÂàÜÂâ≤Á¶ÅÊ≠¢;
				this.#regÂàÜÂâ≤Á¶ÅÊ≠¢ = new RegExp(`[${sÂàÜÂâ≤Á¶ÅÊ≠¢}]`);
			}
			if (this.#s„Å∂„Çâ‰∏ã„Åí	!= s„Å∂„Çâ‰∏ã„Åí) {
				this.#s„Å∂„Çâ‰∏ã„Åí	= s„Å∂„Çâ‰∏ã„Åí;
				this.#reg„Å∂„Çâ‰∏ã„Åí = new RegExp(`[${s„Å∂„Çâ‰∏ã„Åí}]`);
			}
		}
	record() {
		const o: any = {
			break_fixed			: this.break_fixed,
			break_fixed_left	: this.break_fixed_left,
			break_fixed_top		: this.break_fixed_top,
			bura				: this.bura,
		};
		if (this.#sË°åÈ†≠Á¶ÅÂâá === defË°åÈ†≠Á¶ÅÂâá) o.Ë°åÈ†≠Á¶ÅÂâá = this.#sË°åÈ†≠Á¶ÅÂâá;
		if (this.#sË°åÊú´Á¶ÅÂâá === defË°åÊú´Á¶ÅÂâá) o.Ë°åÊú´Á¶ÅÂâá = this.#sË°åÊú´Á¶ÅÂâá;
		if (this.#sÂàÜÂâ≤Á¶ÅÊ≠¢ === defÂàÜÂâ≤Á¶ÅÊ≠¢) o.ÂàÜÂâ≤Á¶ÅÊ≠¢ = this.#sÂàÜÂâ≤Á¶ÅÊ≠¢;
		if (this.#s„Å∂„Çâ‰∏ã„Åí === def„Å∂„Çâ‰∏ã„Åí) o.„Å∂„Çâ‰∏ã„Åí = this.#s„Å∂„Çâ‰∏ã„Åí;
			// „Éá„Éï„Ç©„É´„ÉàÂÄ§„Å®Âêå„Åò„Å™„ÇâJSON„ÅßÁúÅÁï•
		return o;
	}
	playback(hLay: any) {
		if (! hLay) return;		// ÈÄî‰∏≠ËøΩÂä†„Å™„ÅÆ„Åß

		this.#genKinsoku(
			hLay.Ë°åÈ†≠Á¶ÅÂâá ?? defË°åÈ†≠Á¶ÅÂâá,
			hLay.Ë°åÊú´Á¶ÅÂâá ?? defË°åÊú´Á¶ÅÂâá,
			hLay.ÂàÜÂâ≤Á¶ÅÊ≠¢ ?? defÂàÜÂâ≤Á¶ÅÊ≠¢,
			hLay.„Å∂„Çâ‰∏ã„Åí ?? def„Å∂„Çâ‰∏ã„Åí,
		);

		this.break_fixed		= hLay.break_fixed;
		this.break_fixed_left	= hLay.break_fixed_left;
		this.break_fixed_top	= hLay.break_fixed_top;
		this.bura				= hLay.bura;
	}

	hyph(htmTxt: HTMLSpanElement, cnvRect :(rng: Range, ch: string)=> Rectangle, tategaki: boolean, begin: number, bkHtm: string): [IChRect[], number] {
		let a: IChRect[];
		let len = 0;
		let i = 2;		// Êú¨Êù• 1 „Å†„Åå„Å≤„Å®ÊñáÂ≠óÁõÆ„ÅÆË°åÈ†≠Á¶ÅÂâáÊñáÂ≠ó„ÇíÁÑ°Ë¶ñ„Åó„Åü„ÅÑ„ÅÆ„Åß
		let fncFirstChk = (len: number)=> {
			fncFirstChk = ()=> false;

			if (begin === len) {	// Âè≥„ÇØ„É™„ÉÉ„ÇØÊàª„Çä„Å™„Å©„ÅßÊñáÂ≠óË°®Á§∫„ÅåÂ¥©„Çå„Çã‰ª∂„ÅÆÂØæÂøú
				if (begin > 0) htmTxt.innerHTML = bkHtm.replaceAll('class="sn_ch"', 'class="sn_ch sn_ch_in_default"');
				return true;
			}
			return len < 2;
		};
//console.log(`üé¥Á¶ÅÂâáÂá¶ÁêÜÂà§ÂÆö„É´„Éº„Éó begin:${begin}`);
		do {
			a = this.#getChRects(htmTxt, cnvRect);
			len = a.length;
			if (fncFirstChk(len)) break;

			// Á¶ÅÂâáÂá¶ÁêÜÂà§ÂÆö„É´„Éº„Éó
			let sl_xy = -Infinity;	// ÂâçÂõû„ÅÆxy
//console.log(`- len:${len} a_ch:${a.map((v, i)=> v.elm.tagName === 'RT' ?[] :[i, v.ch])}:`);
			for (; i<len; ++i) {
				const {elm, rect, ch} = a[i]!;
				if (elm.tagName === 'RT') continue;	// „É´„Éì„ÅØ„Çπ„Ç≠„ÉÉ„Éó

				const xy = tategaki ?rect.y :rect.x;
//if (sl_xy > 790)
//console.log(`üé¥ sl_xy:${sl_xy.toFixed(2)} xy:${xy.toFixed(2)} i:${i} ch:${ch}: rect:(${rect.left.toFixed(2)}, ${rect.top.toFixed(2)}, ${rect.width.toFixed(2)}, ${rect.height.toFixed(2)})`);
				if (sl_xy <= xy	// „Äê < „Äë„Åß„ÅØ[tcy]‰∫åÊñáÂ≠óÁõÆ„ÇíË™§Âà§ÂÆö„Åô„Çã
				// [r]„Å´„Çà„ÇãÊîπË°åÂæå„ÅØËøΩ„ÅÑÂá∫„ÅóÂá¶ÁêÜ„Çí„Åó„Å™„ÅÑ„Çà„ÅÜ„Å´
				|| (elm.previousElementSibling?.tagName === 'SPAN'
				&&	elm.previousElementSibling?.innerHTML.includes('<br>'))
				// <span class="offrec"> „Äú </span> Â§ñ„Åó
				|| (elm.parentElement?.previousElementSibling?.tagName === 'SPAN'
				&&	elm.parentElement?.previousElementSibling?.innerHTML.includes('<br>'))) {
					sl_xy = xy;
					if (! this.break_fixed) {
						this.break_fixed_left = rect.x;
						this.break_fixed_top = rect.y;
					}
					continue;
				}
/*
	// [r]„Å™„Å©„ÅÆÊîπË°å„ÅØ„Åì„ÅÜ„ÄÇTxtLayer.#tagCh_sub()„Å´„Çà„Çä <span> „Å´ÂÖ•„Çå„Çâ„Çå„Çã
	<span class=‚Äã"sn_ch" style=‚Äã"display:‚Äã inline;‚Äãanimation-delay:‚Äã 10ms;‚Äã">‚Äã
		<br>
	‚Äã</span>‚Äã

	// ‰∏äË®ò„Åå‰∏ãË®ò„Å´Âõ≤„Åæ„Çå„Å¶„ÅÑ„ÇãÂ†¥Âêà„ÇÇ„ÅÇ„Çã„ÄÇpreviousElementSibling ‰ΩøÁî®ÊôÇ„ÅØÊ≥®ÊÑè
	<span class="offrec"> „Äú </span>


	// Á¶ÅÂâáÂá¶ÁêÜ„Å´„Çà„ÇãËá™ÂãïÊîπË°å„ÅØ„Åì„ÅÜ
	<br>
*/

				const p_i = this.#i2pi(a, i);
				const {elm: p_elm, rect: p_rect, ch: p_ch} = a[p_i]!;
//console.log(`üé¥ === Ëá™ÂãïÊîπË°åÁô∫ÁîüÔºÅ ÂâçÊñáÂ≠ó:${p_i}:${p_ch}:%o ‰ªäÊñáÂ≠ó:${i}:${ch}:(${ch.charCodeAt(0).toString(16)})%o`, a[p_i].elm, a[i].elm);
				if (! this.break_fixed) {
					this.break_fixed_left = p_rect.x;
					this.break_fixed_top = p_rect.y;
					const sty = globalThis.getComputedStyle(p_elm);
					const rs = parseFloat(sty.fontSize);
					if (tategaki)	this.break_fixed_top += rs;
					else			this.break_fixed_left += rs;
				}

				sl_xy = -Infinity;	// Ëá™ÂãïÊîπË°åÁô∫ÁîüÔºÅ
				const oldI = i;
				const {cont, ins} = this.bura
					? this.hyph_alg_bura(a, p_i, p_ch, i)
					: this.hyph_alg(a, p_i, p_ch, i, ch);
				i = ins;
//console.log(`üé¥ === cont:${cont} ins:${i} :${a[i].ch}:%o „ÅÆÂâç„Å´ÊîπË°å„ÇíÊåøÂÖ•`, a[i].elm);
				if (cont) continue;

				// ÊîπË°åÊåøÂÖ•
				const elm2 = a[i]!.elm;
				const pal = elm2.parentElement!;
				const br = document.createElement('br');
				if (pal.classList.contains('sn_tx')) pal.insertBefore(br, elm2); else {
					const ppal = pal.parentElement!;
					if (ppal.classList.contains('sn_ch')) {
						ppal.parentElement!.insertBefore(br, ppal);
					}
					else ppal.insertBefore(br, pal);
				}

				i += 2;	// „ÅÑ„ÅæËøΩÂä†„Åó„Åü<br>„ÅÆ„Å∂„Çì„Å®„ÄÅÊ¨°„Å∏Ë°å„Åè +1
				if (i < oldI) i = oldI;	// Ê∞∏‰πÖ„É´„Éº„ÉóÈò≤Âæ°
				len = -1;	// do„É´„Éº„ÉóÂÖàÈ†≠„Å´Êàª„Çã
				break;
			}
		} while (len < 0);

		return [a, len];
	}
		// ‰∏Ä„Å§Ââç„ÅÆË¶ÅÁ¥†„ÇíÊé¢„ÅôÔºà„É´„ÉìÂØæÂøúÔºâ
		#i2pi(a: IChRect[], i: number): number {
			const p_i = i -1;
			const {elm} = a[p_i]!;
			if (elm.tagName !== 'RT') return p_i -(
				elm.style.textCombineUpright === 'all'
				? Array.from(elm.textContent ?? '').length -1
				: 0
			);

			return p_i -Array.from(elm.textContent ?? '').length;
				// „Çµ„É≠„Ç≤„Éº„Éà„Éö„Ç¢ÂØæÁ≠ñ
		}

	#getChRects(elm: Node, cnvRect :(range: Range, ch: string)=> Rectangle): IChRect[] {	// Ê≥®ÊÑèÔºâÂÜçÂ∏∞Èñ¢Êï∞
		const ret: IChRect[] = [];
		if (elm.nodeType !== elm.TEXT_NODE) return Array.from(elm.childNodes).map(v=> this.#getChRects(v, cnvRect)).flat();

		const rng = elm.ownerDocument!.createRange();
		rng.selectNodeContents(elm);
		let pos = 0;
		const end = rng.endOffset;
		// „Åß„Åç„Çå„Å∞‰∏ÄÊñáÂ≠ó„Åö„Å§„Äåafter-edge - baseline„Äç„ÇíË™ø„Åπ„Åü„ÅÑ„Åå„ÄÅÊö´ÂÆöÊâãÊÆµ„ÇíÂèñ„Çã
		//const styles = globalThis.getComputedStyle(this.htmTxt);
		//console.log('lh:'+ styles.lineHeight +' fs:'+ styles.fontSize);
		while (pos < end) {
			rng.setStart(elm, pos);
			rng.setEnd(elm, ++pos);
			const ch = rng.toString();
			ret.push({
				ch,
				rect: cnvRect(rng, ch),
				elm	: rng.startContainer.parentElement!,
			});
		}
		rng.detach();

		return ret;
	}

	/**
	 * ÊäΩË±°Âåñ„Åó„ÅüÁ¶ÅÂâáÂá¶ÁêÜ„Ç¢„É´„Ç¥„É™„Ç∫„É†
	 * @method hyph_alg
	 * @param {IChRect[]} a - ÊñáÁ´†„ÅÆÊäΩË±°ÂåñÈÖçÂàó
	 * @param {number} p_i - Âá¶ÁêÜË¶ÅÁ¥†„ÅÆ‰∏Ä„Å§Ââç„ÅÆÊ∑ªÂ≠ó
	 * @param {string} p_ch - Âá¶ÁêÜË¶ÅÁ¥†„ÅÆ‰∏Ä„Å§Ââç„ÅÆÊñáÂ≠ó
	 * @param {number} i - Âá¶ÁêÜË¶ÅÁ¥†„ÅÆÊ∑ªÂ≠ó
	 * @param {string} ch - Âá¶ÁêÜË¶ÅÁ¥†„ÅÆÊñáÂ≠ó
	 * @return {Object} result Êàª„ÇäÂÄ§
	 * @return {boolean} result.cont - true: Âëº„Å≥ÂÖÉ„ÅßÊîπË°åÊåøÂÖ•„Åõ„Åö continue
	 * @return {number} result.ins - ÊâãÂâç„Å´ÊîπË°å„ÇíÊåøÂÖ•„Åô„Åπ„ÅçË¶ÅÁ¥†„ÅÆÊ∑ªÂ≠ó
	 */
	hyph_alg(	// „ÉÜ„Çπ„ÉàÁî®„Å´public
		a	: IChRect[],
		p_i	: number,
		p_ch: string,
		i	: number,	// i >= 2
		ch	: string,
	): {cont: boolean, ins: number} {
		// ËøΩ„ÅÑÂá∫„ÅóËµ∞Êüª
		if (this.#regË°åÊú´Á¶ÅÂâá.test(p_ch)) {}	// ‰∏Ä„Å§Ââç
		else if (this.#regË°åÈ†≠Á¶ÅÂâá.test(ch)) {	//ÔºàÁèæÂú®Âú∞ -> ÂâçÊñπËµ∞ÊüªÔºâ
			while ((i = this.#i2pi(a, i)) >= 0) {
				if (! this.#regË°åÈ†≠Á¶ÅÂâá.test(a[i]!.ch)) break;// Ë°åÈ†≠Á¶ÅÂâá„ÅØ„Çπ„Ç≠„ÉÉ„Éó
			}
		}
		else if (p_ch === ch && this.#regÂàÜÂâ≤Á¶ÅÊ≠¢.test(p_ch)) {}// ‰∏Ä„Å§ÂâçÔºÜÁèæÂú®Âú∞
		else return {cont: true, ins: i +1};	// ËøΩ„ÅÑÂá∫„Åó„Å™„Åó

		// ËøΩ„ÅÑÂá∫„Åó„Å´„Çà„ÇãÊñ∞Ë°åÊú´‰∫åÊ¨°Âà§ÂÆöÔºà‰∏Ä„Å§Ââç -> ÂâçÊñπËµ∞ÊüªÔºâ
		i = p_i;
		while ((i = this.#i2pi(a, i)) >= 0) {
			if (! this.#regË°åÊú´Á¶ÅÂâá.test(a[i]!.ch)) break;	// Ë°åÊú´Á¶ÅÂâá„ÅØ„Çπ„Ç≠„ÉÉ„Éó
		}
		return {cont: false, ins: i +1};
	}

	/**
	 * ÊäΩË±°Âåñ„Åó„ÅüÁ¶ÅÂâáÂá¶ÁêÜ„Ç¢„É´„Ç¥„É™„Ç∫„É†
	 * @method hyph_alg
	 * @param {IChRect[]} a - ÊñáÁ´†„ÅÆÊäΩË±°ÂåñÈÖçÂàó
	 * @param {number} p_i - Âá¶ÁêÜË¶ÅÁ¥†„ÅÆ‰∏Ä„Å§Ââç„ÅÆÊ∑ªÂ≠ó
	 * @param {string} p_ch - Âá¶ÁêÜË¶ÅÁ¥†„ÅÆ‰∏Ä„Å§Ââç„ÅÆÊñáÂ≠ó
	 * @param {number} i - Âá¶ÁêÜË¶ÅÁ¥†„ÅÆÊ∑ªÂ≠ó
	 * @return {Object} result Êàª„ÇäÂÄ§
	 * @return {boolean} result.cont - true: Âëº„Å≥ÂÖÉ„ÅßÊîπË°åÊåøÂÖ•„Åõ„Åö continue
	 * @return {number} result.ins - ÊâãÂâç„Å´ÊîπË°å„ÇíÊåøÂÖ•„Åô„Åπ„ÅçË¶ÅÁ¥†„ÅÆÊ∑ªÂ≠ó
	 */
	hyph_alg_bura(	// „ÉÜ„Çπ„ÉàÁî®„Å´public
		a	: IChRect[],
		p_i	: number,
		p_ch: string,
		i	: number,	// i >= 2
	): {cont: boolean, ins: number} {
		const pp_i = this.#i2pi(a, p_i);	// ÊîπË°åÂæå„ÅÆ„É´„Éì„ÅßËøΩ„ÅÑÂá∫„ÅóÁô∫Áîü
		const {ch: pp_ch} = a[pp_i]!;
//console.log(`üé¥ hyph_alg_bura pp:${pp_i}:${pp_ch}: p:${p_i}:${p_ch}: i:${i}:${a[i].ch}:`);
		// ÊîπË°åÂâç‰∫åÂÄãÁõÆ„Å´„Äå„Å∂„Çâ‰∏ã„Åí„Äç„Åå„ÅÇ„Çã
		if (this.#reg„Å∂„Çâ‰∏ã„Åí.test(pp_ch) || this.#regË°åÈ†≠Á¶ÅÂâá.test(pp_ch)) {
			let j = p_i;
			// ÊîπË°åÂâç„Å´„ÇÇ„Äå„Å∂„Çâ‰∏ã„Åí„Äç„Åå„ÅÇ„Çã
			if (this.#reg„Å∂„Çâ‰∏ã„Åí.test(p_ch) || this.#regË°åÈ†≠Á¶ÅÂâá.test(p_ch)) ++j;

			// „Å∂„Çâ‰∏ã„ÅíÂæå‚Ä¶‚Ä¶ËøΩ„ÅÑÂá∫„ÅóËµ∞Êüª
			const p_j = this.#i2pi(a, j);	// pp_i „Åß„ÅØ„Å™„ÅÑ
			const {ch: last_ch} = a[p_j]!;	// Ë°åÊú´
			const {ch: head_ch} = a[j]!;		// Ë°åÈ†≠
			// ÂàÜÂâ≤Á¶ÅÊ≠¢
			if (last_ch === head_ch && this.#regÂàÜÂâ≤Á¶ÅÊ≠¢.test(head_ch)) return {cont: false, ins: p_j};

			if (! this.#regË°åÊú´Á¶ÅÂâá.test(last_ch)) return {cont: false, ins: j};
			// Ë°åÊú´Á¶ÅÂâá
			j = p_j;
			do {
				if (! this.#regË°åÊú´Á¶ÅÂâá.test(a[j]!.ch)) break;	// Ë°åÁ¶Å„ÅØ„Çπ„Ç≠„ÉÉ„Éó
			} while ((j = this.#i2pi(a, j)) >= 0);
			return {cont: false, ins: j +1};	// Ë°åÊú´Á¶ÅÂâá
		}

		// ÊîπË°åÂâç‰∫åÂÄãÁõÆ„Å´„Äå„Å∂„Çâ‰∏ã„Åí„Äç„Åå„Å™„ÅÑ
		const ppp_i = this.#i2pi(a, pp_i);
		if (i >= 3) {
			const {ch: ppp_ch} = a[ppp_i]!;
//console.log(`-- ÊîπË°åÂâç‰∫åÂÄãÁõÆ„Å´„Äå„Å∂„Çâ‰∏ã„Åí„Äç„Åå„Å™„ÅÑ ppp:${ppp_i}:${ppp_ch}: pp:${pp_i}:${pp_ch}: i:${i}`);
			// ÊîπË°åÂâç‰∫åÂÄãÁõÆ„Å´„ÄåÂàÜÂâ≤Á¶ÅÊ≠¢„Äç
			if (this.#regÂàÜÂâ≤Á¶ÅÊ≠¢.test(pp_ch)) {	// ÂàÜÂâ≤Á¶ÅÊ≠¢
//console.log(`   == ÊîπË°åÂâç‰∫åÂÄãÁõÆ„Å´„ÄåÂàÜÂâ≤Á¶ÅÊ≠¢„Äç:${ppp_ch === pp_ch}`);
				if (ppp_ch === pp_ch) return {cont: false, ins: ppp_i};
			}
			// ÊîπË°åÂâç‰∏âÂÄãÁõÆ„Å´„ÄåË°åÊú´Á¶ÅÂâá„Äç
			if (this.#regË°åÊú´Á¶ÅÂâá.test(ppp_ch)) {	// Ë°åÊú´Á¶ÅÂâá
				let j = ppp_i;
//console.log(`   == ÊîπË°åÂâç‰∏âÂÄãÁõÆ„Å´„ÄåË°åÊú´Á¶ÅÂâá„Äç j:${j}:${a[j].ch}:`);
				while ((j = this.#i2pi(a, j)) >= 0) {
//console.log(`      -- ch:${a[j].ch}`);
					if (! this.#regË°åÊú´Á¶ÅÂâá.test(a[j]!.ch)) break;// Ë°åÁ¶Å„ÅØ„Çπ„Ç≠„ÉÉ„Éó
				}
//console.log(`   === j:${j +1}:${a[j +1].ch}: tn:${a[j +1].elm.parentElement?.tagName}`);
				return {cont: false, ins: j +1};	// Ë°åÊú´Á¶ÅÂâá
			}
		}

		return {cont: false, ins: pp_i};
	}

}
