	//MARK: „É¨„Ç§„É§„ÅÆ„ÉÄ„É≥„Éó
	#dump_lay({layer}: HArg) {
		console.group('ü•ü [dump_lay]');
		for (const ln of this.#getLayers(layer)) {
			const {fore, back} = this.#hPages[ln]!;
			try {
				console.info(`%c${fore.name.slice(0, -7)} %o`, `color:#${CmnLib.isDarkMode ?'49F' :'05A'};`,
				JSON.parse(`{"back":{${back.dump()}}, "fore":{${fore.dump()}}}`));
			} catch (e) {
				console.error(`dump_lay err:%o`, e);
				console.error(`   back:${back.dump()}`);
				console.error(`   fore:${fore.dump()}`);
			}
		}
		console.groupEnd();

		return false;
	}


	//MARK: „Ç§„Éô„É≥„ÉàÊúâÁÑ°„ÅÆÂàáÊõø
	#enable_event(hArg: HArg) {
		const ln = this.#argChk_layer(hArg, this.#curTxtlay);
		const v = argChk_Boolean(hArg, 'enabled', true);
		this.#getTxtLayer(hArg).enabled = v;
		this.val.setVal_Nochk('save', 'const.sn.layer.'+ ln +'.enabled', v);

		return false;
	}


	//MARK: „Éú„Çø„É≥„ÇíË°®Á§∫
	#button(hArg: HArg) {
		Pages.argChk_page(hArg, 'back');	// „ÉÅ„Çß„ÉÉ„ÇØ„Åó„Åü„ÅÑ„Å®„ÅÑ„ÅÜ„Çà„Çä„Éá„Éï„Ç©„É´„Éà„Çíback„Å´
		hArg.fn ??= this.scrItr.scriptFn;	// „Åì„Åì„ÅßÊåáÂÆö„Åô„ÇãÂøÖË¶Å„Åå„ÅÇ„Çã
			// fnÁúÅÁï•ÊôÇ„ÄÅÁîªÂÉè„Éú„Çø„É≥„ÅØ„É≠„Éº„ÉâÂæå„Å®„ÅÑ„ÅÜÂæå„ÅÆ„Çø„Ç§„Éü„É≥„Ç∞„Åß scrItr.scriptFn „Çí
			// ÂèÇÁÖß„Åó„Å¶„Åó„Åæ„ÅÜ„ÅÆ„Åß
		this.#getTxtLayer(hArg).addButton(hArg);	// hArg[':id_tag'] „ÇÇË®≠ÂÆö

		this.scrItr.recodeDesign(hArg);	// ÂøÖ„Åö[':id_tag'] „ÇíË®≠ÂÆö„Åô„Çã„Åì„Å®

		return false;
	}


	record(): any {
		const o: any = {};
		for (const ln of this.#aLayName) {
			const pg = this.#hPages[ln]!;
			o[ln] = {
				cls: pg.cls,
				fore: pg.fore.record(),
				back: pg.back.record(),
			};
		}
		return o;
	}
	playback($hPages: HIPage): Promise<void>[] {
		// „Åì„Çå„ÇíÂÖà„Å´„ÄÇsave:const.sn.sLog „Åå„ÇØ„É™„Ç¢„Åï„Çå„Å¶„Åó„Åæ„ÅÜ
		this.#aTxtLog = JSON.parse(String(this.val.getVal('save:const.sn.sLog')));
		this.#oLastPage = {text: ''};

		const aPrm: Promise<void>[] = [];
		const aSort: {ln: string, idx: number}[] = [];
		for (const [ln, {fore, fore: {idx}, back, cls}] of Object.entries($hPages)) {	// ÂºïÊï∞„ÅßË®ÄÂèä„ÅÆÁÑ°„ÅÑ„É¨„Ç§„É§„ÅØ„Åù„ÅÆ„Åæ„Åæ„ÄÇÁâπ„Å´ÂâäÈô§„Åó„Å™„ÅÑ
			aSort.push({ln, idx});
			const ps = this.#hPages[ln] ??= new Pages(ln, cls, this.#fore, this.#back, {}, this.sys, this.val, {isWait: false});
			ps.fore.playback(fore, aPrm);
			ps.back.playback(back, aPrm);
		}

		const len = this.#fore.children.length;
		aPrm.push(new Promise(re=> {	// Ëã•„ÅÑÈ†Ü„Å´setChildIndex()
			for (const {ln, idx} of
				aSort.sort(({idx: a}, {idx: b})=> a === b ?0 :a < b ?-1 :1)) {
				const {fore, back} = this.#hPages[ln]!;
				if (! fore) continue;

				const i = len > idx ?idx :len -1;
				this.#fore.setChildIndex(fore.ctn, i);
				this.#back.setChildIndex(back.ctn, i);
			}
			re();
		}));
		return aPrm;
	}

