import {type IEvtMng, argChk_Boolean, argChk_Num} from './CmnLib';
import type {IVariable, IMain} from './CmnInterface';
import {SEARCH_PATH_ARG_EXT} from './ConfigBase';
import type {Config} from './Config';
import type {SysBase} from './SysBase';
import type {HArg} from './Grammar';
import {CmnTween} from './CmnTween';
import {Reading} from './Reading';

import {Loader, LoaderResource} from 'pixi.js';
import {sound, Sound, Options, filters} from '@pixi/sound';
import {Tween, remove} from '@tweenjs/tween.js'

class SndInf {
	static	 #vol_mul_talking = 1;

	stt		: ISndState;
	loop	: boolean 	= false;

	readonly	#proc_id;
	get procID() {return this.#proc_id}

	constructor(
			readonly	fn		: string,
			readonly	buf		: string,
			readonly	start_ms: number,
			readonly	end_ms	: number,
			readonly	ret_ms	: number,
	private	readonly	volume	: number,
			readonly	pan		: number,
			readonly	snd		: Sound,
	) {
		this.stt = snd ?new SsPlaying(this) :new SsLoading;
		this.#proc_id = Reading.procID;
		if (snd) this.addSnd(snd);
	}
	addSnd(snd: Sound) {
		this.loop = snd.loop;
	//	if (! this.loop) sound.add(this.fn, snd);	// ÊâãÂãï„Ç≠„É£„ÉÉ„Ç∑„É•„Åô„Çã„Å®ÂçòÁô∫ÈÄ£Êâì„ÅßÁÑ°Èü≥„Å´

		this.stt.onLoad(this);
		if (this.pan !== 0) snd.filters = [new filters.StereoFilter(this.pan)];

		this.setVol = vol=> snd.volume = vol;
		this.tw = ()=> new Tween(snd);
		this.onPlayEnd = ()=> {this.stt.onPlayEnd(this.buf); this.#onStop()};
		this.stop = ()=> {snd.stop(); this.#onStop()};
		this.destroy = ()=> snd.destroy();

		switch (this.buf) {		// „Çª„É™„ÉïÂÜçÁîü‰∏≠„ÅØBGMÈü≥Èáè„ÇíÁµû„Çã
			case BUF_VOICE:
				const v = Number(val.getVal('sys:sn.sound.BGM.vol_mul_talking') ?? 1);		// Ê≠¥Âè≤ÁöÑÁµåÁ∑Ø„Åß ??
				if (v === 1) break;

				SndInf.#vol_mul_talking = v;
				const b = hSndBuf[BUF_BGM];
				if (b) b.setVol(this.volume * SndInf.#vol_mul_talking);
				break;

			case BUF_BGM:
				snd.volume = this.volume * SndInf.#vol_mul_talking;
				break;
		}
	}
		#onStop = ()=> {
			this.#onStop = ()=> {};
			if (SndInf.#vol_mul_talking === 1 || this.buf !== BUF_VOICE) return;

			// „Éú„É™„É•„Éº„É†„ÇíÊàª„Åô
			SndInf.#vol_mul_talking = 1;
			const b = hSndBuf[BUF_BGM];
			if (b) b.setVol(this.volume * SndInf.#vol_mul_talking);
		}

	setVol(_vol: number) {}
	tw(): Tween<Sound> | undefined {return undefined}
	onPlayEnd() {}
	stop() {}
	destroy() {}
}


let cfg	: Config;
let val	: IVariable;
let main: IMain;
let sys	: SysBase;
let hSndBuf	: HSndBuf;

let evtMng	: IEvtMng;

export interface HSndBuf {[buf: string]: SndBuf}
export	const	BUF_BGM		= 'BGM';
export	const	BUF_SE		= 'SE';
		const	BUF_VOICE	= 'VOICE';


export class SndBuf {
	static	#hLP	: {[buf: string]: string}	= {};
	static	init($cfg: Config, $val: IVariable, $main: IMain, $sys: SysBase, $hSndBuf: HSndBuf) {
		SndBuf.#hLP = {};
		cfg	= $cfg;
		val	= $val;
		main= $main;
		sys	= $sys;
		hSndBuf	= $hSndBuf;
	}
	static	setEvtMng($evtMng: IEvtMng) {evtMng = $evtMng}
	static	delLoopPlay(buf: string): void {
		delete SndBuf.#hLP[buf];
		const vn = 'const.sn.sound.'+ buf +'.';
		val.setVal_Nochk('save', vn +'fn', '');
		val.setVal_Nochk('save', 'const.sn.loopPlaying', JSON.stringify(SndBuf.#hLP));
		val.flush();
	}
	static	getVol(hArg: HArg, def: number): number {
		const vol = argChk_Num(hArg, 'volume', def);
		if (vol < 0) return 0;
		if (vol > 1) return 1;
		return vol;
	}
	static	xchgbuf({buf: buf1 = BUF_SE, buf2 = BUF_SE}: HArg) {
		if (buf1 === buf2) throw `[xchgbuf] buf:${buf1} „ÅåÂêå„ÅòÂÄ§„Åß„Åô`;

		const n1 = 'const.sn.sound.'+ buf1 +'.';
		const v1 = Number(val.getVal('save:'+ n1 +'volume'));
		const f1 = String(val.getVal('save:'+ n1 +'fn'));
		const n2 = 'const.sn.sound.'+ buf2 +'.';
		const v2 = Number(val.getVal('save:'+ n2 +'volume'));
		const f2 = String(val.getVal('save:'+ n2 +'fn'));
		val.setVal_Nochk('save', n1 +'volume', v2);
		val.setVal_Nochk('save', n2 +'volume', v1);
		val.setVal_Nochk('save', n1 +'fn', f2);
		val.setVal_Nochk('save', n2 +'fn', f1);

		if (buf1 in SndBuf.#hLP !== buf2 in SndBuf.#hLP) {	// ÊºîÁÆóÂ≠ê„ÅÆÂÑ™ÂÖàÈ†Ü‰ΩçÁ¢∫Ë™çÊ∏à
			if (buf1 in SndBuf.#hLP)
					{delete SndBuf.#hLP[buf1]; SndBuf.#hLP[buf2] = f1}
			else	{delete SndBuf.#hLP[buf2]; SndBuf.#hLP[buf1] = f2}
			val.setVal_Nochk('save', 'const.sn.loopPlaying', JSON.stringify(SndBuf.#hLP));
		}
		val.flush();
	}


	static	readonly	#MAX_END_MS	= 999000;


	readonly #si		: SndInf;
	readonly needLoad;


	constructor(
		readonly hArg	: HArg,
		readonly buf	: string,
		readonly fn		: string,
	) {
		const start_ms = argChk_Num(hArg, 'start_ms', 0);
		const end_ms = argChk_Num(hArg, 'end_ms', SndBuf.#MAX_END_MS);
		const ret_ms = argChk_Num(hArg, 'ret_ms', 0);
		const pan = argChk_Num(hArg, 'pan', 0);
		const speed = argChk_Num(hArg, 'speed', 1);

		if (start_ms < 0) throw `[playse] start_ms:${start_ms} „ÅåË≤†„ÅÆÂÄ§„Åß„Åô`;
		if (ret_ms < 0) throw `[playse] ret_ms:${ret_ms} „ÅåË≤†„ÅÆÂÄ§„Åß„Åô`;
		if (0 < end_ms) {
			if (end_ms <= start_ms) throw `[playse] start_ms:${start_ms} >= end_ms:${end_ms} „ÅØÁï∞Â∏∏ÂÄ§„Åß„Åô`;
			if (end_ms <= ret_ms) throw `[playse] ret_ms:${ret_ms} >= end_ms:${end_ms} „ÅØÁï∞Â∏∏ÂÄ§„Åß„Åô`;
		}

		// „Åì„ÅÆËæ∫„ÅßÂ±ûÊÄß„ÇíÂ¢óÊ∏õ„Åó„Åü„Çâ„ÄÅloadFromSaveObj()„Å´„ÇÇÂèçÊò†„Åô„Çã
		const vn = 'const.sn.sound.'+ buf +'.';
		val.setVal_Nochk('save', vn +'fn', fn);
		const savevol = SndBuf.getVol(hArg, 1);
		val.setVal_Nochk('save', vn +'volume', savevol);// ÁõÆÊ®ôÈü≥ÈáèÔºàsave:Ôºâ
		const volume = savevol * Number(val.getVal('sys:'+ vn +'volume', 1));

		const loop = argChk_Boolean(hArg, 'loop', false);
		if (loop) {
			SndBuf.#hLP[buf] = fn;
			val.setVal_Nochk('save', 'const.sn.loopPlaying', JSON.stringify(SndBuf.#hLP));
		}
		else SndBuf.delLoopPlay(buf);
		val.setVal_Nochk('save', vn +'start_ms', start_ms);
		val.setVal_Nochk('save', vn +'end_ms', end_ms);
		val.setVal_Nochk('save', vn +'ret_ms', ret_ms);
		val.setVal_Nochk('tmp', vn +'playing', true);
		val.flush();

		const snd = sound.find(fn);	// „Ç≠„É£„ÉÉ„Ç∑„É•„Å´„ÅÇ„Çã„Åã
		this.#si = new SndInf(
			fn,
			buf,
			start_ms,
			end_ms,
			ret_ms,
			volume,
			pan,
			snd,
		);
		// @pixi/soundÁî®Âü∫Êú¨„Éë„É©„É°„Éº„Çø
		const o: Options = {
			loop,
			speed,
			volume,
			loaded	: (e, s2)=> {
				if (this.#si.stt.isDestroy) return;
				if (e) {main.errScript(`„É≠„Éº„ÉâÂ§±Êïó„Åß„Åô SndBuf fn:${fn} ${e}`, false); return}
				if (! s2) return;

				this.#si.addSnd(s2);
				if (pan !== 0) s2.filters = [new filters.StereoFilter(pan)];
			//	if (! o.loop) sound.add(fn, snd);	// ÊâãÂãï„Ç≠„É£„ÉÉ„Ç∑„É•„Åô„Çã„Å®ÂçòÁô∫ÈÄ£Êâì„ÅßÁÑ°Èü≥„Å´
				hArg.fnc?.();
			},
		};

		// start_ms„Éªend_msÊ©üËÉΩ‚Üí@pixi/soundÊ∫ñÂÇô
		let sp_nm = '';
		if (0 < start_ms || end_ms < SndBuf.#MAX_END_MS) {
			sp_nm = `${fn};${start_ms};${end_ms};${ret_ms}`;
			const os = (o.sprites ??= {})[sp_nm] = {
				start	: start_ms /1000,
				end		: end_ms /1000,
			};
			o.preload = true;		// loadedÁô∫ÁîüÁî®„ÄÅ„Éà„É©„Éñ„É´„ÅÆÂÖÉ„Å™„ÅÆ„Åß‰ΩøÁî®„ÇíÊéß„Åà„Åü„ÅÑ
			const old = o.loaded!;
			o.loaded = (e, s0)=> {
				if (this.#si.stt.isDestroy) return;

				old(e, s0);
				const s2 = s0!;
				const d = s2.duration;
				if (os.end < 0) {	// Ë≤†„ÅÆÂÄ§„ÅØÊú´Â∞æ„Åã„Çâ
					os.end += d;
					s2.removeSprites(sp_nm);
					s2.addSprites(sp_nm, os);
				}
				if (os.end <= os.start) main.errScript(`[playse] end_ms:${end_ms}(${os.end *1000}) >= start_ms:${start_ms} „ÅØÁï∞Â∏∏ÂÄ§„Åß„Åô`);
				if (os.end *1000 <= ret_ms) main.errScript(`[playse] end_ms:${end_ms}(${os.end *1000}) <= ret_ms:${ret_ms} „ÅØÁï∞Â∏∏ÂÄ§„Åß„Åô`);
				if (d <= os.start) main.errScript(`[playse] Èü≥Â£∞„Éï„Ç°„Ç§„É´ÂÜçÁîüÊôÇÈñì:${d *1000} <= start_ms:${start_ms} „ÅØÁï∞Â∏∏ÂÄ§„Åß„Åô`);
				if (end_ms !== SndBuf.#MAX_END_MS && d <= os.end) main.errScript(`[playse] Èü≥Â£∞„Éï„Ç°„Ç§„É´ÂÜçÁîüÊôÇÈñì:${d *1000} <= end_ms:${end_ms} „ÅØÁï∞Â∏∏ÂÄ§„Åß„Åô`);

				s2.play(sp_nm, snd=> o.complete?.(snd));
					// ÊµÅ„ÇåÁöÑ„Å´„ÅØ„Åô„Åê‰∏ã„ÅÆ„Äå„É´„Éº„Éó„Å™„Åó/„ÅÇ„Çä„Äç„ÇíÂëº„Å∂
			};
		}
		else o.autoPlay = true;

		// „É´„Éº„Éó„Å™„Åó ... ÂÜçÁîüÂÆå‰∫Ü„Ç§„Éô„É≥„Éà
		if (! loop) o.complete = ()=> {
			stop2var(this.#si, buf);
			this.#si.onPlayEnd();
		};
		// „É´„Éº„Éó„ÅÇ„Çä ... ret_msÂá¶ÁêÜ
		else if (ret_ms !== 0) {
			o.loop = false;	// ‰∏ÄÂë®ÁõÆ„ÅØ„É´„Éº„Éó„Å™„Åó„Å®„Åô„Çã
			o.complete = async snd=> {
				const d = snd.duration;
				const start	= ret_ms /1000;
				const end	= end_ms /1000;
				if (d <= start) main.errScript(`[playse] Èü≥Â£∞„Éï„Ç°„Ç§„É´ÂÜçÁîüÊôÇÈñì:${d *1000} <=  ret_ms:${ret_ms} „ÅØÁï∞Â∏∏ÂÄ§„Åß„Åô`);

				await snd.play({	// ‰∏ÄÂë®ÁõÆ„ÅØ„É´„Éº„Éó„Å™„Åó„ÄÅ„Å™„ÅÆ„Åß„Ç≠„É£„ÉÉ„Ç∑„É•„Åï„Çå„Å¶„Çã
					...o,
					start,
					end		: (end < 0) ?end +d :end,// Ë≤†„ÅÆÂÄ§„ÅØÊú´Â∞æ„Åã„Çâ
				//	speed,		// ÈáçË§á
					loop	: true,
				//	volume,		// ÈáçË§á
				//-	muted?: boolean;
					filters	: pan !== 0 ?[new filters.StereoFilter(pan)] :[],
				//-	complete?: CompleteCallback;
				//-	loaded?: LoadedCallback;
				//-	singleInstance?: boolean;
				});
				//‰∏çË¶Å this.#sb.snd = snd;	// this.#sb.snd === snd (true)
			};
		}

		this.#initVol();
		if (snd) {
			snd.volume = volume;	// ÂÜçÁîü„ÅÆ„Åü„Å≥„Å´Èü≥Èáè„ÇíÊàª„Åô
			if (sp_nm) {
				this.#playseSub(fn, o);
				if (pan !== 0) snd.filters = [new filters.StereoFilter(pan)];
			}
			else if (snd.isPlayable) {
				const ab = snd.options.source;
				if (! (ab instanceof ArrayBuffer) || ab.byteLength === 0) {
					snd.play(o);
					if (pan !== 0) snd.filters = [new filters.StereoFilter(pan)];
				}
				else this.#si.addSnd(Sound.from({
					...o,
					url		: snd.options.url,
					source	: ab,
				}));
			}
			this.needLoad = false;
			return;
		}

		const join = this.needLoad = argChk_Boolean(hArg, 'join', true);
		if (join) {
			const RPN_LOADED = this.#si.procID +`loaded buf:${buf} fn:${fn}`;
			Reading.beginProc(RPN_LOADED);
			const old = o.loaded!;
			o.loaded = (e, s2)=> {
				old(e, s2);
				Reading.endProc(RPN_LOADED);
			};
		}
		this.#playseSub(fn, o);
	}
	#initVol = ()=> {
		sound.volumeAll = Number(val.getVal('sys:sn.sound.global_volume', 1));
		this.#initVol = ()=> {};
	};
	#playseSub(fn: string, o: Options) {
		const src = cfg.searchPath(fn, SEARCH_PATH_ARG_EXT.SOUND);
		if (! src.endsWith('.bin')) {o.url = src; Sound.from(o); return}

		(new Loader).add({name: fn, url: src, xhrType: LoaderResource.XHR_RESPONSE_TYPE.BUFFER,})
		.use(async (res, next)=> {
			try {
				res.data = await sys.decAB(res.data);
			} catch (e) {
				main.errScript(`Sound „É≠„Éº„ÉâÂ§±Êïó„Åß„Åôc fn:${res.name} ${e}`, false);
			}
			next();
		})
		.load((_ldr, hRes)=> {	// „Åì„ÅÆ„ÅÇ„Å® o.loaded() „ÇÇ„Ç≥„Éº„É´„Åï„Çå„Çã
			o.source = hRes[fn]?.data;
			Sound.from(o);
		});
	}


	setVol(vol: number) {this.#si.setVol(vol)}

	ws =(hArg: HArg)=> this.#si.stt.ws(this.#si, hArg);
	stopse({buf = BUF_SE}: HArg) {
		stop2var(this.#si, buf);
		this.#si.stt.stopse(this.#si);
	}

	fade =(hArg: HArg)=> this.#si.stt.fade(this.#si, hArg);
	wf =(hArg: HArg)=> this.#si.stt.wf(this.#si, hArg);
	stopfadese =(hArg: HArg)=> this.#si.stt.stopfadese(this.#si, hArg);

}


// =================================================
function stop2var({loop}: SndInf, buf: string) {
	if (loop) {SndBuf.delLoopPlay(buf); return}

	const vn = 'const.sn.sound.'+ buf +'.';
	val.setVal_Nochk('tmp', vn +'playing', false);
	val.flush();
}

// =================================================


interface ISndState {
	onLoad(si: SndInf)	: void;
	stopse(si: SndInf)	: void;
	ws(si: SndInf, hArg: HArg): boolean;
	onPlayEnd(buf: string)	: void;
	fade(si: SndInf, hArg: HArg): void;
	wf(si: SndInf, hArg: HArg): boolean;
	compFade(buf: string)	: void;
	stopfadese(si: SndInf, hArg: HArg): void;
	isDestroy	: boolean;
}

class SsLoading implements ISndState {
	onLoad(si: SndInf)	{si.stt = new SsPlaying(si)}
	stopse(si: SndInf)	{si.stt = new SsStop(si, false)}
	ws =()=> false;
	onPlayEnd() {}		// ok
	fade() {}			// ok
	wf =()=> false;		// ok
	compFade() {}		// ok
	stopfadese() {}		// ok
	readonly	isDestroy	= false;
}

class SsPlaying implements ISndState {
	constructor(readonly si: SndInf) {}
	onLoad() {}			// ok
	stopse(si: SndInf)	{si.stt = new SsStop(si)}
	ws(si: SndInf, hArg: HArg) {
		if (si.loop) return false;

		si.stt = new SsWaitingStop(si);
		const canskip = argChk_Boolean(hArg, 'canskip', false);
		const stop = argChk_Boolean(hArg, 'stop', true);
		if (canskip && evtMng.isSkipping) {
			if (stop) si.stt.stopse(si); else si.stt.onPlayEnd = ()=> {};
			return false;
		}

		const {buf = BUF_SE} = hArg;
		const fin = ()=> {
			stop2var(si, buf);
			si.onPlayEnd();	// „Åæ„Åö‰∏ÄÂõû„ÇÑ„Çã
			if (stop) si.stt.stopse(si); else si.stt.onPlayEnd = ()=> {};
				// elseÂæå„ÅØ SsWaitingStop „Åã SsStop „ÅÆÊÉ≥ÂÆö
		};
		Reading.beginProc(si.procID +'ws', fin, true, canskip ?fin :undefined);
		return true;
	}
	onPlayEnd() {}		// ok
	fade(si: SndInf, hArg: HArg) {
		const {buf = BUF_SE} = hArg;

		const vn = 'const.sn.sound.'+ buf +'.';
		const bnV = vn +'volume';
		const savevol = SndBuf.getVol(hArg, NaN);
		val.setVal_Nochk('save', bnV, savevol);	// ÁõÆÊ®ôÈü≥ÈáèÔºàsave:Ôºâ
		const vol = savevol * Number(val.getVal('sys:'+ bnV, 1))
		const stop = argChk_Boolean(hArg, 'stop', (savevol === 0));
			// this.getVol() „Å´„Çà„Çä savevol = hArg.volume
		if (stop) SndBuf.delLoopPlay(buf);	// fade‰∏≠reload„Å™„Å©„ÄÅ„Åß„Åç„Çã„Å†„ÅëÊó©„ÅèÊÉÖÂ†±Êõ¥Êñ∞„Åã
		val.flush();

		const time = argChk_Num(hArg, 'time', NaN);
		const delay = argChk_Num(hArg, 'delay', 0);
		if ((time === 0 && delay === 0) || evtMng.isSkipping) {
			si.setVol(vol);
			si.stt = stop ? new SsStop(si) : new SsPlaying(si);
			return;
		}

//console.log('fadese start from:%f to:%f', sb.snd.volume, vol);
		const tw = si.tw();
		if (! tw) return;
		CmnTween.setTwProp(tw, hArg)
		.to({volume: vol}, time)
		.onComplete(()=> {
			remove(tw);
			si.stt.compFade(buf);
			si.stt = stop ? new SsStop(si) : new SsPlaying(si);
		})
		.start();

		si.stt = new SsFade(tw, si);
	}
	wf =()=> false;		// ok
	compFade() {}		// ok
	stopfadese() {}		// ok
	readonly	isDestroy	= false;
}

class SsWaitingStop implements ISndState {
	constructor(readonly si: SndInf) {}
	onLoad() {}			// ok
	stopse(si: SndInf)	{si.stt = new SsStop(si)}
	ws =()=> false;		// ok
	onPlayEnd()	{Reading.notifyEndProc(this.si.procID +'ws')}
	fade() {}			// ok
	wf =()=> false;		// ok
	compFade() {}		// ok
	stopfadese() {}		// ok
	readonly	isDestroy	= false;
}

class SsFade implements ISndState {
	constructor(readonly tw: Tween<Sound>, private readonly si: SndInf) {}
	onLoad() {}			// ok
	stopse(si: SndInf)	{this.stopfadese(); si.stt = new SsStop(si)}	// È†ÜÁï™Âé≥ÂÆà
	ws =()=> false;		// ok ?
	onPlayEnd() {}		// ok
	fade() {}			// ok
	wf(si: SndInf, hArg: HArg) {
		si.stt = new SsWaitingFade(si);
		const canskip = argChk_Boolean(hArg, 'canskip', false);
		if (canskip && evtMng.isSkipping) {this.stopfadese(); return false}

		const fin = ()=> this.stopfadese();
		Reading.beginProc(si.procID +'wf', fin, true, canskip ?fin :undefined);
		return true;
	}
	compFade() {}		// ok
	stopfadese =()=> this.si.stop();
	readonly	isDestroy	= false;
}

class SsWaitingFade implements ISndState {
	constructor(private readonly si: SndInf) {}
	onLoad() {}			// ok
	stopse(si: SndInf)	{this.stopfadese(); si.stt = new SsStop(si)}
	ws =()=> false;		// ok
	onPlayEnd() {}		// ok
	fade() {}			// ok
	wf =()=> false;		// ok
	compFade() {Reading.notifyEndProc(this.si.procID +'wf')}
	stopfadese =()=> this.si.stop();
	readonly	isDestroy	= false;
}

class SsStop implements ISndState {
	constructor(readonly si: SndInf, readonly stop = true) {
		if (! stop) return;

		si.stop();
		if (! si.loop) return;	// destroy „Åå„Å™„ÅÑ„Å®ÂÜçÁîü„ÅåÊÆã„Çã„Ç±„Éº„Çπ„Åå„ÄÇÂäπÊûúÈü≥„Å†„Å®Á†¥Ê£Ñ„ÅåÊøÄ„Åó„ÅÑ„ÅÆ„Åß„É´„Éº„Éó„É¢„Éé(BGM)„Å†„Åë„Å´„Åô„Çã

		si.destroy();
		si.destroy = ()=> {};	// ÂÜçÂ∫¶„Ç≥„Éº„É´ÊôÇ„Ç®„É©„ÉºÂØæÁ≠ñ
	}
	onLoad() {}			// ok
	stopse() {}			// ok
	ws =()=> false;		// ok
	onPlayEnd() {}		// ok
	fade() {}			// ok
	wf =()=> false;		// ok
	compFade() {}		// ok
	stopfadese() {}		// ok
	readonly	isDestroy	= true;
}
