import {Layer} from './Layer';
import {uint, CmnLib, type IEvtMng, argChk_Boolean, argChk_Num, initStyle, addStyle, argChk_Color} from './CmnLib';
import type {IHTag, HArg} from './Grammar';
import type {IVariable, IPutCh, IRecorder} from './CmnInterface';
import {TxtStage} from './TxtStage';
import type {Config} from './Config';
import {RubySpliter} from './RubySpliter';
import {SpritesMng} from './SpritesMng';
import {Button} from './Button';
import {LayerMng, type IMakeDesignCast} from './LayerMng';
import type {SysBase} from './SysBase';
import {DebugMng} from './DebugMng';
import {SEARCH_PATH_ARG_EXT} from './ConfigBase';
import {Reading} from './Reading';
import type {ScriptIterator} from './ScriptIterator';

import {Sprite, DisplayObject, Graphics, Container, Renderer, Application} from 'pixi.js';


export class TxtLayer extends Layer {
	static	#cfg		: Config;
	static	#val		: IVariable;
	static	#isPageFore	: (me: TxtLayer)=> boolean;
	static	#recorder	: IRecorder;
	static	init(cfg: Config, hTag: IHTag, val: IVariable, recorder: IRecorder, isPageFore: (me: TxtLayer)=> boolean, appPixi: Application): void {
		TxtLayer.#cfg = cfg;
		TxtStage.init(cfg, appPixi);
		TxtLayer.#val = val;
		TxtLayer.#recorder = recorder;
		TxtLayer.#isPageFore = isPageFore;

		val.setDoRecProc(TxtLayer.chgDoRec);

		hTag.autowc			= o=> TxtLayer.#autowc(o);	// ÊñáÂ≠ó„ÇíËøΩÂä†„Åô„Çã
		hTag.autowc({enabled: false, text: '', time: 0});
		hTag.ch_in_style	= o=> TxtLayer.#ch_in_style(o);	// ÊñáÂ≠óÂá∫ÁèæÊºîÂá∫
		hTag.ch_out_style	= o=> TxtLayer.#ch_out_style(o);	// ÊñáÂ≠óÊ∂àÂéªÊºîÂá∫

		// „ÇÆ„É£„É©„É™„Éº„É™„É≠„Éº„ÉâÁî®ÂàùÊúüÂåñ
		TxtStage.initChStyle();
		initStyle();

		addStyle(
			cfg.matchPath('.+', SEARCH_PATH_ARG_EXT.FONT)
			.flatMap(o=> Object.values(o).map(v=> `
@font-face {
	font-family: '${v}';
	src: url('${this.#cfg.searchPath(v, SEARCH_PATH_ARG_EXT.FONT)}');
}
`)).join('')

			// ÊñáÂ≠óÂá∫ÁèæÊºîÂá∫Èñ¢‰øÇ
			+`
.sn_tx {
	pointer-events: none;
	user-select: none;
	-webkit-touch-callout: none;
	box-sizing: border-box;
}
.sn_ch {
	position: relative;
	display: inline-block;
}
`	// „Äåsn_ch„Äç„Å®„Äåsn_ch_in_„Äú„Äç„ÅÆ‰∏≠Ë∫´„ÅåÈáçË§á„Åó„Å¶„ÅÑ„Çã„Åå„ÄÅ„Åì„Çå„ÅØÂøÖÈ†à
		);

		TxtLayer.#ch_in_style({
			name	: 'default',
			wait	: 500,
			alpha	: 0,
			x		: '=0.3',
			y		: '=0',
			scale_x	: 1,
			scale_y	: 1,
			rotate	: 0,
			join	: true,
			ease	: 'ease-out',
		});
		TxtLayer.#ch_out_style({
			name	: 'default',
			wait	: 0,
			alpha	: 0,
			x		: '=0',
			y		: '=0',
			scale_x	: 1,
			scale_y	: 1,
			rotate	: 0,
			join	: false,
			ease	: 'ease-out',
		});
	}

	// ÊñáÂ≠óÂá∫ÁèæÊºîÂá∫
	static	#ch_in_style(hArg: HArg) {
		const o = TxtStage.ch_in_style(hArg);
		const x = o.x.startsWith('=') ?`${o.nx *100}%` :`${o.nx}px`;
		const y = o.y.startsWith('=') ?`${o.ny *100}%` :`${o.ny}px`;
		const {name} = hArg;
		addStyle(`
.sn_ch_in_${name} {
	position: relative;
	display: inline-block;
}
.go_ch_in_${name} {
	opacity: ${o.alpha};
	position: relative;
	display: inline-block;
	animation: sn_ch_in_${name} ${o.wait}ms ${o.ease} 0s both;
}
@keyframes sn_ch_in_${name} {
	from {transform: rotate(${o.rotate}deg) scale(${o.scale_x}, ${o.scale_y}) translate(${x}, ${y})}
	to {opacity: 1; transform: none;}
}
`);

		return false;
	}
	// ÊñáÂ≠óÊ∂àÂéªÊºîÂá∫
	static	#ch_out_style(hArg: HArg) {
		const o = TxtStage.ch_out_style(hArg);
		const x = o.x.startsWith('=') ?`${o.nx *100}%` :`${o.nx}px`;
		const y = o.y.startsWith('=') ?`${o.ny *100}%` :`${o.ny}px`;
		const {name} = hArg;
		addStyle(`
.go_ch_out_${name} {
	position: relative;
	display: inline-block;
	animation: go_ch_out_${name} ${o.wait}ms ${o.ease} 0s both;
}
@keyframes go_ch_out_${name} {
	to {
		opacity: ${o.alpha};
		transform: rotate(${o.rotate}deg) scale(${o.scale_x}, ${o.scale_y}) translate(${x}, ${y});
	}
`);

		return false;
	}


	static	#evtMng	: IEvtMng;
	static	#sys	: SysBase;
	static setEvtMng(evtMng: IEvtMng, sys: SysBase, scrItr: ScriptIterator) {
		TxtLayer.#evtMng = evtMng;
		TxtLayer.#sys = sys;
		TxtStage.setEvtMng(evtMng, scrItr);
	}

	// ÊñáÂ≠ó„Åî„Å®„ÅÆ„Ç¶„Çß„Ç§„Éà
	static #doAutoWc	= false;
	static #hAutoWc	: {[ch: string]: number}	= {};
	static #autowc(hArg: HArg) {
		TxtLayer.#doAutoWc = argChk_Boolean(hArg, 'enabled', TxtLayer.#doAutoWc);
		TxtLayer.#val.setVal_Nochk('save', 'const.sn.autowc.enabled', TxtLayer.#doAutoWc);

		const {text} = hArg;
		if (('text' in hArg) !== ('time' in hArg)) throw '[autowc] text„Å®time„ÅØÂêåÊôÇÊåáÂÆöÂøÖÈ†à„Åß„Åô';
		TxtLayer.#val.setVal_Nochk('save', 'const.sn.autowc.text', text);
		if (! text) {
			TxtLayer.#val.setVal_Nochk('save', 'const.sn.autowc.time', '');
			return false;
		}

		const len = text.length;
		if (TxtLayer.#doAutoWc && len === 0) throw '[autowc] enabled === false „Åã„Å§ text === "" „ÅØË®±„Åï„Çå„Åæ„Åõ„Çì';

		const a = String(hArg.time).split(',');
		if (a.length !== len) throw '[autowc] textÊñáÂ≠óÊï∞„Å®time„Å´Ë®òËø∞„Åï„Çå„ÅüÂæÖ„Å°ÊôÇÈñìÔºà„Ç≥„É≥„ÉûÂå∫Âàá„ÇäÔºâ„ÅØÂêåÊï∞„Å´„Åó„Å¶‰∏ã„Åï„ÅÑ';
		TxtLayer.#hAutoWc = {};	// ÊØéÂõû„ÇØ„É™„Ç¢„Çí‰ªïÊßò„Å®„Åô„Çã
		for (let i=0; i<len; ++i) TxtLayer.#hAutoWc[text[i]!] = uint(a[i]);
		TxtLayer.#val.setVal_Nochk('save', 'const.sn.autowc.time', hArg.time);

		return false;
	}


	// „Éê„ÉÉ„ÇØ
	#b_color			= 0x000000;
	#b_alpha			= 0;
	#b_alpha_isfixed	= false;
	#b_do			: DisplayObject | undefined	= undefined;
	#b_pic			= '';	// ËÉåÊôØÁîªÂÉèÁÑ°„ÅóÔºàÔºùÂçòËâ≤Â°ó„ÇäÔºâ

	// ÊñáÂ≠óË°®Á§∫
	#txs	= new TxtStage(this.ctn, ()=> this.canFocus(), TxtLayer.#sys);

	readonly	#rbSpl	= new RubySpliter;
	readonly	#htmRb	= document.createElement('span');// css„ÉÅ„Çß„ÉÉ„ÇØ„Éª‰øùÂ≠òÁî®
	static	readonly	#hWarnR_Style = {
		'text-align'		: 0,
		'text-align-last'	: 0,
		'height'	: 0,
		'width'		: 0,
		'padding-left'		: 0,
		'padding-right'		: 0,
		'padding-top'		: 0,
		'padding-bottom'	: 0,
	};

	readonly	#cntBtn: Container<Button>	= new Container;

	constructor() {
		super();

		this.ctn.addChild(this.#txs);

		this.#rbSpl.init(this.#putCh);

		this.ctn.addChild(this.#cntBtn);	// „Éú„Çø„É≥„ÅØpadding„ÅÆÂΩ±Èüø„ÇíÂèó„Åë„Å™„ÅÑ
		this.#cntBtn.name = 'cntBtn';

		const padding = 16;	// ÂàùÊúüpadding
		this.lay({style: `width: ${CmnLib.stageW}px; height: ${CmnLib.stageH}px; font-family: 'Hiragino Sans', 'Hiragino Kaku Gothic ProN', 'Ê∏∏„Ç¥„Ç∑„ÉÉ„ÇØ Medium', meiryo, sans-serif; color: white; font-size: 24px; line-height: 1.5; padding: ${padding}px;`, in_style: 'default', out_style: 'default', back_clear: 'true'});
	}
	override destroy() {
		if (this.#b_do) {this.ctn.removeChild(this.#b_do).destroy(); this.#b_do = undefined}

		TxtLayer.#recorder.recPagebreak();
		this.#txs.destroy();
	}
	static	destroy() {
		TxtLayer.#doAutoWc = false;
		TxtLayer.#hAutoWc = {};

		TxtLayer.#rec = tx=> tx;
	}
	override set name(nm: string) {this.name_ = nm; this.#txs.name = nm}
	override get name() {return this.name_}	// get„ÅØÁ∂ôÊâø„Åó„Å™„ÅÑ„Çâ„Åó„ÅÑ


	override cvsResize() {this.#txs.cvsResize()}
	override cvsResizeChildren() {
		for (const b of this.#cntBtn.children) b.cvsResize();
	}

	protected	override	procSetX(x: number) {this.#txs.lay({x})}
	protected	override	procSetY(y: number) {this.#txs.lay({y})}

	override lay(hArg: HArg) {
		super.lay(hArg);
		Layer.setXY(this.ctn, hArg, this.ctn);

		hArg[':id_tag'] = this.name_.slice(0, -7);	// DesignÁî®
		RubySpliter.setting(hArg);
		this.#setFfs(hArg);
		this.#txs.lay(hArg);

		if ('r_align' in hArg) this.#r_align = hArg.r_align ?? '';
		this.#ruby_pd = CmnLib.isSafari
		? this.#txs.tategaki		// Safari„Åß„ÅØË¶™ÊñáÂ≠óÂπÖ l „ÅØÁñë‰ººÂÄ§
			? (v, l)=> `text-align: start; height: ${l}em; padding-top: ${v}; padding-bottom: ${v};`
			: (v, l)=> `text-align: start; width: ${l}em; padding-left: ${v}; padding-right: ${v};`
		: this.#txs.tategaki
			? v=> `text-align: justify; text-align-last: justify; padding-top: ${v}; padding-bottom: ${v};`
			: v=> `text-align: justify; text-align-last: justify; padding-left: ${v}; padding-right: ${v};`;
		if (CmnLib.isFirefox) this.#mkStyle_r_align = this.#mkStyle_r_align4ff;

		if ('r_style' in hArg) {
			if (hArg.r_style) {
				const cln = document.createElement('span');
				cln.style.cssText = hArg.r_style;
				const len = cln.style.length;
				const s = this.#htmRb.style;
				for (let i=0; i<len; ++i) {
					const key: any = cln.style[i];
					if (key in TxtLayer.#hWarnR_Style) {
						DebugMng.myTrace(`${key}„ÅØÊåáÂÆö„Åß„Åç„Åæ„Åõ„Çì`, 'W');
						continue;
					}
					const v = cln.style[key];
					if (v) s[key] = v;
				}
	//			if ((! cln.style.opacity) && ('alpha' in hArg)) s.opacity = String(this.spLay.alpha);
			}
			else this.#htmRb.style.cssText = '';
		}

		if ('alpha' in hArg) for (const b of this.#cntBtn.children) b.alpha = this.ctn.alpha;

		this.#set_ch_in(hArg);
		this.#set_ch_out(hArg);

		const RPN_TXT_LAY = Reading.procID +`TxtLayer lay name:${this.name_}`;
		const ret = this.#drawBack(hArg, isStop=> {
			if (isStop) Reading.endProc(RPN_TXT_LAY);
		});
		if (ret) Reading.beginProc(RPN_TXT_LAY);
		return ret;
	}
	#set_ch_in(hArg: HArg) {
		const {in_style} = hArg;
		if (! in_style) return;
		const cis = TxtStage.getChInStyle(in_style);
		if (! cis) throw `Â≠òÂú®„Åó„Å™„ÅÑin_style„Äê${in_style}„Äë„Åß„Åô`;
		this.#$ch_in_style = in_style;
		this.#ch_in_join = cis.join;
	}
	#$ch_in_style	= '';
	#ch_in_join		= true;

	override get	width() {return this.#txs.getWidth}
	override get	height() {return this.#txs.getHeight}

	#set_ch_out(hArg: HArg) {
		const {out_style} = hArg;
		if (! out_style) return;
		const cos = TxtStage.getChOutStyle(out_style);
		if (! cos) throw `Â≠òÂú®„Åó„Å™„ÅÑout_style„Äê${out_style}„Äë„Åß„Åô`;
		this.#$ch_out_style = out_style;
	}
	#$ch_out_style	= '';

	#sps	= new SpritesMng;
	#drawBack(hArg: HArg, fncComp: (isStop: boolean)=> void): boolean {
		if ('back_clear' in hArg) {
			if (argChk_Boolean(hArg, 'back_clear', false)) {
				this.#b_color = 0x000000;
				this.#b_alpha = 0;
				this.#b_alpha_isfixed = false;
				this.#b_pic = '';
			}
			fncComp(false);
			return false;
		}

		this.#b_alpha = argChk_Num(hArg, 'b_alpha', this.#b_alpha);
		this.#b_alpha_isfixed = argChk_Boolean(hArg, 'b_alpha_isfixed', this.#b_alpha_isfixed);
		const alpha = (this.#b_alpha_isfixed
			? 1
			: Number(TxtLayer.#val.getVal('sys:TextLayer.Back.Alpha'))
		) *this.#b_alpha;
		if (hArg.b_pic) {
			if (this.#b_pic !== hArg.b_pic) {
				this.#b_pic = hArg.b_pic;
				if (this.#b_do) {
					this.ctn.removeChild(this.#b_do);	// Graphics „Åã„ÇÇ
					this.#b_do.destroy();	// Graphics „Åã„ÇÇ
				}
				//this.#sps.destroy();	// Graphics „Åã„ÇÇ„Å™„ÅÆ„Åß‰ΩøÁî®‰∏çÂèØ
				this.#sps = new SpritesMng(this.#b_pic, this.ctn, sp=> {
					this.#b_do = sp;
					sp.name = 'back(pic)';
					sp.visible = (alpha > 0);
					sp.alpha = alpha;
					//CmnLib.adjustRetinaSize(this.b_pic, sp);
					this.#txs.setMySize(sp.width, sp.height);
						// „Å°„Å™„Åø„Å´Â∑¶‰∏äË°®Á§∫‰ΩçÁΩÆ„ÅØÊú¨„É¨„Ç§„É§„Å®Âêå„Åò
					this.ctn.setChildIndex(sp, 0);
					fncComp(true);
				});
				return this.#sps.ret;
			}
		}
		else if ('b_color' in hArg) {
			this.#b_color = argChk_Color(hArg, 'b_color', 0x000000);
			if (this.#b_do) {
				this.ctn.removeChild(this.#b_do);	// Graphics „Åã„ÇÇ
				this.#b_do.destroy();	// Graphics „Åã„ÇÇ
				//this.#sps.destroy();	// Graphics „Åã„ÇÇ„Å™„ÅÆ„Åß‰ΩøÁî®‰∏çÂèØ
			}
			this.#b_pic = '';	// Âøò„Çå„Åö„ÇØ„É™„Ç¢
			this.ctn.addChildAt(
				(this.#b_do = new Graphics)
				.beginFill(this.#b_color, alpha)
				.lineStyle(undefined)
				.drawRect(0, 0, this.#txs.getWidth, this.#txs.getHeight)
				.endFill(), 0);
			this.#b_do.name = 'back(color)';
			//cacheAsBitmap = true;	// „Åì„Çå„ÇíÊúâÂäπ„Å´„Åô„Çã„Å®„Çπ„Éä„ÉÉ„Éó„Ç∑„Éß„ÉÉ„Éà„ÅåÊíÆ„Çå„Å™„ÅÑÔºüÔºü
		}

		if (this.#b_do) {
			this.#b_do.visible = (alpha > 0);
				// ÈÄèÊòé„ÅÆÊôÇ„ÅØË°®Á§∫„Åó„Å™„ÅÑ„ÄÇ„Åì„ÅÜ„Åó„Å™„ÅÑ„Å®ÈÄèÊòé„ÉÜ„Ç≠„Çπ„Éà„É¨„Ç§„É§‰∏ã„ÅÆ„Éú„Çø„É≥„Åå
				// Êäº„Åõ„Å™„Åè„Å™„ÇãÔºàÈÄèÊòé„Å†„ÅåÂ°ó„Çä„Åå„ÅÇ„Çã„Å®„ÅÑ„ÅÜÊâ±„ÅÑ„Å™„ÅÆ„ÅßÔºâ
			this.#b_do.alpha = alpha;
		}
		fncComp(false);

		return false;
	}
	chgBackAlpha(g_alpha: number): void {
		const alpha = this.#b_alpha_isfixed
			? this.#b_alpha
			: g_alpha * this.#b_alpha;
		if (this.#b_do instanceof Graphics) {
			if (this.#b_do) {
				this.ctn.removeChild(this.#b_do);
				this.#b_do.destroy();
			}
			this.ctn.addChildAt(
				(this.#b_do = new Graphics)
				.beginFill(this.#b_color, alpha)
				.lineStyle(undefined)
				.drawRect(0, 0, this.#txs.getWidth, this.#txs.getHeight)
				.endFill(), 0);
			this.#b_do.name = 'back(color)';
			//cacheAsBitmap = true;	// „Åì„Çå„ÇíÊúâÂäπ„Å´„Åô„Çã„Å®„Çπ„Éä„ÉÉ„Éó„Ç∑„Éß„ÉÉ„Éà„ÅåÊíÆ„Çå„Å™„ÅÑÔºüÔºü
		}
		if (this.#b_do) {
			this.#b_do.visible = (alpha > 0);
				// ÈÄèÊòé„ÅÆÊôÇ„ÅØË°®Á§∫„Åó„Å™„ÅÑ„ÄÇ„Åì„ÅÜ„Åó„Å™„ÅÑ„Å®ÈÄèÊòé„ÉÜ„Ç≠„Çπ„Éà„É¨„Ç§„É§‰∏ã„ÅÆ„Éú„Çø„É≥„Åå
				// Êäº„Åõ„Å™„Åè„Å™„ÇãÔºàÈÄèÊòé„Å†„ÅåÂ°ó„Çä„Åå„ÅÇ„Çã„Å®„ÅÑ„ÅÜÊâ±„ÅÑ„Å™„ÅÆ„ÅßÔºâ
			this.#b_do.alpha = alpha;
		}
	}

	#setFfs(hArg: HArg) {
		if ('noffs' in hArg) {
			this.#strNoFFS = hArg.noffs ?? '';
			this.#regNoFFS = new RegExp(`[„ÄÄ${this.#strNoFFS}]`);
		}
		if (! ('ffs' in hArg)) return;

		this.#ffs ??= '';
		this.#fncFFSStyle = (this.#ffs === '')
			? ()=> ''
			: ch=> this.#regNoFFS.test(ch)
				? ''
				: ` font-feature-settings: ${this.#ffs};`;
	}
	#ffs	= '';
	#fncFFSStyle	= (_ch: string)=> '';
	#strNoFFS	= '';
	#regNoFFS	= /[„ÄÄ]/;
	// Safari„ÅåÂÖ®‰Ωì„Å´„Äåfont-feature-settings„Äç„Åó„ÅüÂæå„ÄÅÁâπÂÆöÊñáÂ≠ó„ÅÆ„Äåfont-feature-settings: initial;„Äç„ÇíÂèó„Åë‰ªò„Åë„Å¶„Åè„Çå„Å™„ÅÑ„ÅÆ„Åß„Çè„Åñ„Çè„Åñ‰∏Ä„Å§„Åö„Å§ÊåáÂÆö


	static	chgDoRec(doRec: boolean) {
		TxtLayer.#rec = doRec
			? tx=> tx
			: tx=> `<span class='offrec'>${tx}</span>`;
				// Âõ≤„Çì„Å†È†òÂüü„ÅØÂ±•Ê≠¥„ÅßÈùûË°®Á§∫
	}
	static	#rec = (tx: string)=> tx;

	isCur	= false;
	#ruby_pd: (v: string, l: number)=> string = ()=> '';
	#mkStyle_r_align: (ch: string, rb: string, r_align: string, add_sty?: string)=> string = (ch, rb, r_align, add_sty='')=> {
		if (! r_align) return ` style='${add_sty}'`;

		const len = ch.length *2;
		if (len -rb.length < 0) return ` style='text-align: ${r_align}; ${add_sty}'`;

		let st = '';
		switch (r_align) {
		case 'justify':
			st = this.#ruby_pd('0', len);	break;
		case '121':
			st = this.#ruby_pd(`calc(${(len -rb.length) /(rb.length *2)}em)`, len);	break;
		case 'even':
			st = this.#ruby_pd(`calc(${(len -rb.length) /(rb.length +1)}em)`, len);	break;
		case '1ruby':
			st = this.#ruby_pd('1em', len);	break;
		default:
			st = `text-align: ${r_align};`;
		}
		return ` style='${st} ${add_sty}'`;
	};
	#r_align	= '';
	#mkStyle_r_align4ff(ch: string, rb: string, r_align: string, add_sty=''): string {
		if (! r_align) return ` style='${add_sty}'`;

		const len = ch.length *2;
		if (len -rb.length < 0) return ` style='text-align: ${r_align}; ${add_sty}'`;

		let st = '';
		switch (r_align) {
			case 'left':	st = `ruby-align: start;`;	break;
			case 'center':	st = `ruby-align: center;`;	break;
			case 'right':	// „Ç®„É¨„Ç¨„É≥„Éà„Å´„Çµ„Éù„Éº„Éà„Åß„Åç„Å¶„ÅÑ„Å™„ÅÑ
				st = `ruby-align: start;`;	break;
			case 'justify':	st = `ruby-align: space-between;`;	break;
			case '121':		st = `ruby-align: space-around;`;	break;
			case 'even':
				const ev = (len -rb.length) /(rb.length +1);
				st = `ruby-align: space-between; `+
				(this.#txs.tategaki
					? `padding-top: ${ev}em; padding-bottom: ${ev}em;`
					: `padding-left: ${ev}em; padding-right: ${ev}em;`);
				break;
			case '1ruby':	st = `ruby-align: space-between; `+
				(this.#txs.tategaki
					? `padding-top: 1em; padding-bottom: 1em;`
					: `padding-left: 1em; padding-right: 1em;`);
				break;
			default:		st = `text-align: ${r_align};`;
		}
		return ` style='${st} ${add_sty}'`;
	}

	tagCh(text: string): void {this.#rbSpl.putTxt(text)}
	#needGoTxt = false;
	get needGoTxt() {return this.#needGoTxt}
	readonly	#putCh	: IPutCh = (ch, ruby)=> {
		if (TxtLayer.#cfg.oCfg.debug.putCh) console.log(`üñä ÊñáÂ≠óË°®Á§∫ text:\`${ch}\`(${ch.charCodeAt(0).toString(16)}) ruby:\`${ruby}\` name:\`${this.name_}\``);

		const a_ruby = ruby.split('ÔΩú');
		let add_htm = '';
		const [a0, ...a1_] = a_ruby;
		const a1 = a1_.join('ÔΩú');
		switch (a_ruby.length) {
		case 1:		// Â≠óorÊò•„Ää„ÅØ„Çã„Äã
			this.#needGoTxt = true;
			if (ch === '\n') {
				if (this.#firstCh) {// ÔºëÊñáÂ≠óÁõÆ„Å´„É´„Éì„ÅåÁÑ°„ÅÑÂ†¥Âêà„ÄÅ‰∏çÂèØË¶ñ„É´„Éì„ÅßË°åÊèÉ„Åà
					this.#firstCh = false;
					add_htm = '<ruby>&emsp;<rt>&emsp;</rt></ruby><br/>';
				}
				else add_htm = '<br/>';
				break;
			}
			if (this.#firstCh) {	// ÔºëÊñáÂ≠óÁõÆ„Å´„É´„Éì„ÅåÁÑ°„ÅÑÂ†¥Âêà„ÄÅ‰∏çÂèØË¶ñ„É´„Éì„ÅßË°åÊèÉ„Åà
				this.#firstCh = false;
				if (ruby === '') ruby = '&emsp;';
			}
			add_htm = this.#tagCh_sub(ch, ruby, this.#r_align);
			break;

		default:	// „ÄägrpÔΩú{"id":"break","pic":"breakline"}„Äã
			switch (a0) {	// „É´„ÉìÊèÉ„ÅàÊåáÂÆö„Å®ÂêåÊôÇ„Ç∑„É™„Éº„Ç∫
			case 'start':	// ÂàùÊúüÂÄ§
			case 'left':	//ÔºàËÇ©‰ªò„ÅçÔºâÂÖàÈ†≠Ë¶™ÊñáÂ≠ó„Åã„Çâ„ÄÅ„É´„ÉìÈñì„ÅØÂØÜÁùÄ
			case 'center':	//Ôºà‰∏≠‰ªò„ÅçÔºâ„Çª„É≥„Çø„ÉºÂêà„Çè„Åõ„ÄÅ„ÄÉ
			case 'right':	//ÔºàÂè≥Ôºè‰∏ãÊèÉ„ÅàÔºâÊú´Â∞æË¶™ÊñáÂ≠ó„Åã„Çâ„ÄÅ„ÄÉ
			case 'justify':	//Ôºà‰∏°Á´ØÊèÉ„ÅàÔºâÂÖàÈ†≠„Åã„ÇâÊú´Â∞æË¶™ÊñáÂ≠óÈñì„Å´„ÄÅ„É´„ÉìÈñì„ÅØÂùáÁ≠â„Å´„ÅÇ„Åë„Çã
			case '121':		//Ôºà1-2-1(JIS)Ôºâ„É´„Éì„ÅÆÂâçÂæå„ÇíÊØîÁéá1„ÄÅ„É´„ÉìÈñì„ÇíÊØîÁéá2„Åß„ÅÇ„Åë„Çã
			case 'even':	//ÔºàÂùáÁ≠â„Ç¢„Ç≠Ôºâ„É´„Éì„ÅÆÂâçÂæå„ÄÅ„É´„ÉìÈñì„ÇÇÂùáÁ≠â„Å´„ÅÇ„Åë„Çã
			case '1ruby':	//Ôºà1„É´„ÉìÊñáÂ≠ó„Ç¢„Ç≠Ôºâ„É´„Éì„ÅÆÂâçÂæå„Çí„É´„Éì‰∏ÄÊñáÂ≠óÁ©∫„Åë„ÄÅ„É´„ÉìÈñì„ÅØÂùáÁ≠â„Å´„ÅÇ„Åë„Çã
				this.#firstCh = false;
				this.#needGoTxt = true;
				add_htm = this.#tagCh_sub(ch, a1, a0);
				break;

			case 'gotxt':{
				this.#popSpan();
				if (this.#needGoTxt) {
					if (this.isCur) TxtLayer.#recorder.recText(
						this.#aSpan.join('')
						.replace(/^<ruby>&emsp;<rt>&emsp;<\/rt><\/ruby>(<br\/>)+/, '')
							// ÂâçÊñπ„ÅÆÁ©∫Ë°å„Çítrim
						.replaceAll(/style='(anim\S+ [^;]+;\s*)+/g, `style='`)
							// 2 matches (40 step, 205¬µs) https://regex101.com/r/u1H6v2/1
						.replaceAll(/( style=''| data-(add|arg|cmd)='[^']+'|\n+|\t+)/g, '')
							// 2 matches (94 step, 175¬µs) https://regex101.com/r/5DFUmv/1
						.replaceAll(/class='sn_ch[^']+/g, `class='sn_ch`)
							// ‰∏çË¶ÅÊÉÖÂ†±ÂâäÈô§
							// 2 matches (28 step, 280¬µs) https://regex101.com/r/XhBbYo/1
						.replaceAll(`display: none;`, '')	// Â±•Ê≠¥ÊÉÖÂ†±ÂèØË¶ñÂåñ
						.replaceAll(`class='offrec'`, `style='display: none;'`)
							// Âõ≤„Çì„Å†È†òÂüü„ÅØÂ±•Ê≠¥„ÅßÈùûË°®Á§∫
					);

					this.#txs.goTxt(this.#aSpan, this.#cumDelay === 0);
					this.#needGoTxt = false;
					this.#cumDelay = 0;
				}
				else if (this.isCur) this.#txs.noticeCompTxt();
			}	return;	// break„Åß„ÅØ„Å™„ÅÑ

			case 'add':{	// ÊñáÂ≠óÂπÖ„ÇíÊåÅ„Åü„Å™„ÅÑÊ±éÁî®ÁöÑ„Å™ÂëΩ‰ª§ÔºàÂøÖ„Åöadd_close„Åô„Çã„Åì„Å®Ôºâ
				const o = JSON.parse(a1);

				const {style='', wait=null} = o;// wait=0„Å´„Åô„Çã„Å®ËÉåÊôØ„ÅåÊúÄÂàù„Åã„ÇâÂá∫„Çã
				const {cl, sty} = this.#o2domArg(true, wait);
				this.#aSpan.push(`<span${cl} style='${sty} display: inline; ${style}'>`);	// display: inline; „Åå„Å™„ÅÑ„Å®Ââç„ÅÆÊîπË°å„ÅåÊîπË°å„Åó„Å™„Åè„Å™„Çã
				delete o.style;	// ËÉåÊôØstyle„Å™„Å©„Çí[ch]„Åß‰∏ÄÂ°ä„Å®„Åô„Çã
				this.#pushSpan(o);
			}	return;	// break„Åß„ÅØ„Å™„ÅÑ
			case 'add_close':
				this.#aSpan.push('</span>');
				this.#popSpan();
				return;	// break„Åß„ÅØ„Å™„ÅÑ

			case 'grp':	//	ÁîªÂÉè„Å™„Å© „ÄägrpÔΩú{"id":"break","pic":"breakline"}„Äã
				this.#needGoTxt = true;
			{
				const o = JSON.parse(a1);
				o.id ??= this.#aSpan.length;
				if (o.id === 'break') {this.#txs.dispBreak(o); return}	// break„Åß„ÅØ„Å™„ÅÑ

				this.#firstCh = false;
				o.delay = this.#cumDelay;	// ÁîªÂÉè„ÅÆ„Çπ„É©„Ç§„Éâ„Ç§„É≥„Åß‰Ωø„ÅÜ
				o.r ??= '';
				o.style ??= '';
				o.r_style ??= '';
				const {cl, sty, lnk} = this.#o2domArg(true, o.wait);
				add_htm = `<span${cl} style='${sty} ${o.style
				}'><ruby><span data-cmd='grp' data-arg='${JSON.stringify(o)
				}'${lnk} style='${sty} display: inline;'>&emsp;</span><rt${lnk}${
					this.#mkStyle_r_align('„ÄÄ', o.r, this.#r_align,
					this.#htmRb.style.cssText	// „Åì„ÅÆ‰∏¶„Å≥„Åß‰∏äÊõ∏„Åç„Åï„Çå„Å¶„ÅÑ„Åè
					+ (this.#stkASpan.at(-1)?.o.r_style ?? '')
					+ o.r_style)
				}>${o.r}</rt></ruby></span>`;
			}
				break;

			case 'tcy':	// „É´„Éì‰ªò„ÅçÁ∏¶‰∏≠Ê®™
				this.#firstCh = false;
				this.#needGoTxt = true;
			{
				const {t, r='', wait=null, style='', r_style=''} = <{
					t: string, r?: string, wait? :number,
					style? :string, r_style? :string,
				}>JSON.parse(a1);
				if (TxtLayer.#val.doRecLog()) {
					this.#page_text += ch +(ruby ?`„Ää${ruby}„Äã` :'');
					this.#page_plain_text += t;
				}

				const rb = CmnLib.isSafari
					? r.replaceAll(/[A-Za-z0-9]/g, s=> String.fromCharCode(s.charCodeAt(0) + 65248))
						// Ëã±Êï∞Â≠ó„ÇíÂÖ®Ëßí„Å´(Safari„ÅßÁ∏¶‰∏≠Ê®™„É´„Éì„ÅåÂçäËßíÊñáÂ≠ó„Å†„Å®„ÄÅ
						// ÈÅ∏ÊäûÁü©ÂΩ¢„ÅåÊ®™ÂÄí„Åó„Å´„Å™„Çã‰∏çÂÖ∑ÂêàÂØæÁ≠ñ)
					: r;
				const {cl, sty, lnk} = this.#o2domArg(true, wait);
				add_htm = `<span${cl} style='${sty}${this.#fncFFSStyle(t)} ${style}'><ruby><span${lnk} style='${sty} display: inline; text-combine-upright: all;'>${t}</span><rt${lnk}${
					this.#mkStyle_r_align(t, rb, this.#r_align,
					this.#htmRb.style.cssText	// „Åì„ÅÆ‰∏¶„Å≥„Åß‰∏äÊõ∏„Åç„Åï„Çå„Å¶„ÅÑ„Åè
					+ (this.#stkASpan.at(-1)?.o.r_style ?? '')
					+ r_style)
				}>${rb}</rt></ruby></span>`;
			}
				break;

			case 'del':	//if (a1 !== 'break') throw 'ÊñáÂ≠ó„É¨„Ç§„É§del„Ç≥„Éû„É≥„Éâ„ÅØ„ÄÅÁèæÂú®id=break„ÅÆ„Åø„Çµ„Éù„Éº„Éà„Åó„Åæ„Åô';
				TxtStage.delBreak();
				return;	// break„Åß„ÅØ„Å™„ÅÑ

			case 'span':
				this.#needGoTxt = true;
				this.#mergePushSpan(JSON.parse(a1));
				return;	// break„Åß„ÅØ„Å™„ÅÑ

			case 'link':
				this.#needGoTxt = true;
			{
				const o = JSON.parse(a1);
				o[':link'] = ` data-lnk='@'`;
				const {cl, sty, curpos} = this.#o2domArg(false, o.wait);
				this.#aSpan.push(`<span${cl} style='${sty} display: inline; ${o.style ?? ''}' ${curpos} data-arg='${a1}'>`);
					// display: inline; „ÇíÂâäÈô§„Åô„Çã„Å®„É´„Éì„ÇÇ„É™„É≥„ÇØËÉåÊôØ„Å´Âê´„Åæ„Çå„Çã
					// „Åü„Å†„ÅóÂΩì„Åü„ÇäÂà§ÂÆö„ÅØÊñáÂ≠ó„Éª„É´„Éì‰∏ä„ÅÆ„Åø
				delete o.style;
				this.#mergePushSpan(o);
			}	return;	// break„Åß„ÅØ„Å™„ÅÑ

			case 'endlink':
				this.#needGoTxt = true;
				this.#aSpan.push('</span>');
				this.#popSpan();
				return;	// break„Åß„ÅØ„Å™„ÅÑ

			default:	// „É´„Éì„ÅÇ„ÇäÊñáÂ≠óÂàó
				this.#needGoTxt = true;
				add_htm = this.#tagCh_sub(ch, ruby, this.#r_align);
			}
			break;
		}
		this.#aSpan.push(TxtLayer.#rec(add_htm));
	}
	#tagCh_sub(ch: string, rb: string, r_align: string): string {
		const ht = ch===' ' ?'&nbsp;' :ch==='„ÄÄ' ?'&emsp;' :ch;
		if (TxtLayer.#val.doRecLog()) {
			this.#page_text += ht +(rb ?`„Ää${rb}„Äã` :'');
			if (ch !== ' ') this.#page_plain_text += ch;
		}
		const {cl, sty, lnk} = this.#o2domArg(true, null, ch);

		if (rb) return `<span${cl} style='${sty} ${this.#fncFFSStyle(ch)
		//<ruby style='ruby-position:under;'>	// ‰∏ã‰ªò„É´„Éì
		}'><ruby>${	// ÊñáÂ≠óÂÄãÂà•„Å´Âá∫Áèæ„Åï„Åõ„Çã„Åü„ÇÅ‰ª•‰∏ã„Å´„ÇÇ ${cl} „ÅåÂøÖË¶Å
			Array.from(ch).map((c, i)=> `<span${cl}${lnk} style='${
				i > 0 ?this.#o2domArg(true, null, ch).sty :sty
			} display: inline;'>${
				c===' ' ?'&nbsp;' :c==='„ÄÄ' ?'&emsp;' :c
			}</span>`).join('')
				// display: inline; „Åå„Å™„ÅÑ„Å®[span]Âå∫ÈñìÂ§ñ„ÅÆ„É´„Éì„ÅåÊµÆ„Åè
		}<rt${lnk}${this.#mkStyle_r_align(ch, rb, r_align,
			this.#htmRb.style.cssText	// „Åì„ÅÆ‰∏¶„Å≥„Åß‰∏äÊõ∏„Åç„Åï„Çå„Å¶„ÅÑ„Åè
			+ (this.#stkASpan.at(-1)?.o.r_style ?? ''))
		}>${rb}</rt></ruby></span>`;	// <span>„Å´ÂÖ•„Çå„Å™„ÅÑ„Å®Â¥©„Çå‰∏ÄÊñáÂ≠ó„Åö„Å§Âá∫„Å™„ÅÑ

		return `<span${cl} style='${sty} ${this.#fncFFSStyle(ch)}'${lnk}>${ht}</span>`;
	}
	#o2domArg(isAddWait: boolean, argWait: number | null, ch = '\n') {
		const wait = this.#ch_in_join ?(argWait
			?? this.#stkASpan.at(0)?.o.wait	// Ë¶™Ë¶ÅÁ¥†
			?? (TxtLayer.#doAutoWc	// ()„ÅØÂøÖË¶Å
				? TxtLayer.#hAutoWc[ch.at(0) ?? ''] ?? 0
				: LayerMng.msecChWait)) :0;
		if (TxtLayer.#evtMng.isSkipping) this.#cumDelay = 0;
	//	else if (isAddWait && this.#ch_in_join) this.#cumDelay += Number(wait)*5;	// Âá∫ÁèæË¶ñË™ç„ÉÜ„Çπ„ÉàÁî®
		else if (isAddWait && this.#ch_in_join) this.#cumDelay += Number(wait);
		const curpos = `data-add='{"ch_in_style":"${this.#$ch_in_style}", "ch_out_style":"${this.#$ch_out_style}"}'`;

		return {
			cl	: ` class='sn_ch sn_ch_yet sn_ch_in_${this.#$ch_in_style}'`,	// TxtStage.goTxt()„ÅØ„Åì„ÇåÂçò‰Ωç„ÅßÊñáÂ≠óÂá∫Áèæ„Åï„Åõ„Çã
			sty	: `animation-delay: ${this.#cumDelay}ms;${
				this.#stkASpan.at(-1)?.o.style ?? ''
			}`,		// TxtStage.goTxt()„ÅØ„Åì„ÇåÂçò‰Ωç„ÅßÊñáÂ≠óÂá∫Áèæ„Åï„Åõ„Çã
			lnk	: (this.#stkASpan.at(0)?.o[':link'] ?? '')+' '+ curpos,
			curpos,
		};
	}
	#cumDelay	= 0;
	#firstCh	= true;
	#aSpan		: string[]	= [];
	#stkASpan	: {
		o		: {
			style?		: string;
			r_style?	: string;
			':link'?	: string;
			wait?		: number;
		};
		r_align			: string;
		ch_in_style		: string;
		ch_out_style	: string;
	}[] = [];

	#pushSpan(o :any) {
		this.#stkASpan.push({
			o,
			r_align			: this.#r_align,
			ch_in_style		: this.#$ch_in_style,
			ch_out_style	: this.#$ch_out_style,
		});
		if ('r_align' in o) this.#r_align = o.r_align;
		this.#set_ch_in(o);
		this.#set_ch_out(o);
	}
	#popSpan() {
		const stk = this.#stkASpan.pop();
		if (! stk) return;

		this.#r_align = stk.r_align;
		this.#set_ch_in({in_style: stk.ch_in_style});
		this.#set_ch_out({out_style: stk.ch_out_style});
	}
	#mergePushSpan(o :any) {
		const stk = this.#stkASpan.at(-1);
		if (! stk) {this.#pushSpan(o); return}

		stk.o = {...stk.o, ...o};
		if (! o.style && ! o.r_style) {stk.o.style = ''; stk.o.r_style = ''}
			// „Å©„Å°„Çâ„ÇÇÊåáÂÆö„Åï„Çå„Å¶„Å™„Åë„Çå„Å∞„ÇØ„É™„Ç¢
		if ('r_align' in o) this.#r_align = o.r_align;
		this.#set_ch_in(o);
		this.#set_ch_out(o);
	}

	readonly click = ()=> {	// true: ÊñáÂ≠óÂá∫Áèæ‰∏≠„Å†„Å£„Åü„ÅÆ„Åß„ÄÅÂÅúÊ≠¢„Åô„Çã
		if (! this.ctn.interactiveChildren || ! this.ctn.visible) return false;
		return this.#txs.skipChIn();
	}

	clearText(): void {
		this.ctn.addChild(this.#txs = this.#txs.reNew());

		this.#cumDelay = 0;
		this.#firstCh = true;
		this.#aSpan = [];
		this.#page_text = '';
		this.#page_plain_text = '';
		TxtLayer.#recorder.recPagebreak();
	}
	#page_text			= '';
	#page_plain_text	= '';
	get pageText() {return this.#page_text.replace('„Ää&emsp;„Äã', '')}
	get pagePlainText() {return this.#page_plain_text}

	get enabled() {return this.ctn.interactiveChildren}
	set enabled(e) {this.ctn.interactiveChildren = e}

	readonly	addButton = (hArg: HArg)=> new Promise<void>(re=> {
		hArg.key = `btn=[${this.#cntBtn.children.length}] `+ this.name_;
		hArg[':id_tag'] = hArg.key.slice(0, -7);	// DesignÁî®
		argChk_Boolean(hArg, 'hint_tate', this.#txs.tategaki);	// hintÁî®
		const btn = new Button(hArg, TxtLayer.#evtMng, ()=> re(), ()=> this.canFocus());
		btn.name = JSON.stringify(hArg).replaceAll('"', "'");// playbackÊôÇ„Å´‰ΩøÁî®
		this.#cntBtn.addChild(btn);
	});
	canFocus(): boolean {
		return (this.ctn.interactiveChildren ?? false)
		&& this.ctn.visible
		&& TxtLayer.#isPageFore(this);
	}


	override clearLay(hArg: HArg): void {
		super.clearLay(hArg);

		this.clearText();
		// ‰∏ä„ÅßÂëº„Å∞„Çå„Çã this.#evtMng.escapeHint();	// Hint„Åî„Å®destroy„Åï„Çå„Çã„ÅÆ„ÇíÂõûÈÅø
		for (const b of this.#cntBtn.removeChildren()) b.destroy();
	}
	override readonly record = ()=> {return <any>{...super.record(),
		enabled	: this.enabled,

		r_cssText	: this.#htmRb.style.cssText,
		r_align		: this.#r_align,

		// „Éê„ÉÉ„ÇØ
		b_do	: (this.#b_do === undefined)
					? undefined
					: (this.#b_do instanceof Sprite ?'Sprite' :'Graphics'),
		b_pic	: this.#b_pic,
		b_color	: this.#b_color,
		b_alpha	: this.#b_alpha,
		b_alpha_isfixed	: this.#b_alpha_isfixed,

		ffs		: this.#ffs,
		txs		: this.#txs.record(),
		strNoFFS: this.#strNoFFS,

		btns	: this.#cntBtn.children.map(b=> b.name),
	}};
	override playback(hLay: any, aPrm: Promise<void>[]): void {
		super.playback(hLay, aPrm);

		this.enabled	= hLay.enabled;

		this.#htmRb.style.cssText = hLay.r_cssText;
		this.#r_align	= hLay.r_align;
		this.cvsResize();

		this.#setFfs(hLay);
		this.#txs.playback(hLay.txs);
		// ÊñáÂ≠óËÉåÊôØ„Çµ„Ç§„Ç∫„ÅØ TxtStage „ÇíÂèÇÁÖß„Åô„Çã„ÅÆ„Åß„Åì„ÅÆÈ†Ü„Åß
		this.#b_alpha			= hLay.b_alpha;
		this.#b_alpha_isfixed	= hLay.b_alpha_isfixed;

		aPrm = [
			aPrm,
			new Promise<void>(re=> {
				const h: HArg = (hLay.b_do)
					? (hLay.b_do === 'Sprite'
						? {b_pic: hLay.b_pic}
						: {b_color: hLay.b_color})
					: {b_pic: ''};
				h.b_alpha = hLay.b_alpha;
				h.b_alpha_isfixed = hLay.b_alpha_isfixed;
				if (! this.#drawBack(h, isStop=> {if (isStop) re()})) re();
			}),
			(hLay.btns as string[]).map(b=> new Promise<void>(re=> {
				this.addButton(JSON.parse(b.replaceAll(`'`, '"'))); re()
			})),
		].flat();
	}

	get cssText() {return this.#txs.cssText}
	set cssText(ct: string) {this.#txs.cssText = ct}

	override snapshot(rnd: Renderer, re: ()=> void) {
		rnd.render(this.ctn, {clear: false});
		this.#txs.snapshot(rnd, re);
	}
	override snapshot_end() {this.#txs.snapshot_end()}

	override makeDesignCast(gdc: IMakeDesignCast) {
		if (! this.ctn.visible) return;
		this.#txs.makeDesignCast(gdc);
	}
	override makeDesignCastChildren(gdc: IMakeDesignCast) {
		if (! this.ctn.visible) return;
		for (const b of this.#cntBtn.children) b.makeDesignCast(gdc);
	}

	override showDesignCast() {this.#txs.showDesignCast()}
	override showDesignCastChildren() {
		for (const b of this.#cntBtn.children) b.showDesignCast();
	}

	override dump(): string {
		this.#putCh('', 'gotxtÔΩú');	// „Éê„ÉÉ„Éï„Ç°„ÅÆÊñáÂ≠ó„ÇíÂç∞Â≠ó„Åó„Å¶„Åó„Åæ„ÅÜ

		return super.dump() +`, "enabled":"${this.enabled}", ${this.#txs.dump()
		}, "b_pic":"${this.#b_pic}", "b_color":"${this.#b_color
		}", "b_alpha":${this.#b_alpha}, "b_alpha_isfixed":"${this.#b_alpha_isfixed
		}", "width":${this.#txs.getWidth}, "height":${this.#txs.getHeight
		}, "pixi_obj":[${
			this.ctn.children.map(e=> `{"class":"${
				(e instanceof Sprite) ?'Sprite' :(
					(e instanceof Graphics) ?'Graphics' :(
						(e instanceof Container) ?'Container' :'?'
					)
				)
			}", "name":"${e.name}", "alpha":${e.alpha}, "x":${e.x}, "y":${e.y}, "visible":"${e.visible}"}`).join(',')
		}], "button":[${
			this.#cntBtn.children.map(b=> b.children[0]?.name ?? '{}').join(',')
		}]`;
	}

}
